
import Table from 'react-bootstrap/Table';
import React, { useRef, useState, useEffect } from 'react';
import Row from 'react-bootstrap/Row';
import { Link } from 'react-router-dom';
import Col from 'react-bootstrap/Col';
import Popup from 'reactjs-popup';
function Login(props) {
    const [Users, SetUser] = useState([""]);
  
    const [status, Setstatus] = useState('');
    var totalamount = 0;
    var totalqty = 0;
    useEffect(() => {
        getlist()
    }, []);
    // var  Spinner =  true;
    function getlist() {
        fetch("http://192.168.1.7:4000/order/", {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
            },
        })
            .then((result) => {
                result.json()
                    .then((data) => {
                        SetUser(data.list)
                        
                        console.log("data", data.list);


                    })
            })
    }

    const senddata = (props) => {
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
        console.log(totalamount, totalqty,Users )
        let date = {totalamount,totalqty,Users }
        fetch("http://192.168.1.7:4000/confirm/", {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-type': 'application/json',
                'Authorization': user_data.jwt,
            },
            body: JSON.stringify(date)

        })
            .then(response => response.json())
            .then((resData) => {
                console.log(resData)
                // props.history.push('./Logout');
            }
            )
    }
    return (
        <Row>
            <Col xs={12} md={12} lg={12} >

                {Users.map((items, index) => {
                    totalamount += items.amount;
                    totalqty += items.qty
                    return (<div class="card-header2">
                        <div class="row">
                            <div class="col-lg-8 col-md-8">
                                <h6> Total Amount: {totalamount}</h6>
                            </div>
                            <div class="col-lg-2 col-md-2">    <h6>Total Qty : {totalqty}</h6>   </div>
                            <div class="col-lg-2 col-md-2">    <Popup trigger={<Link type="submit" className="btn btn-success " >Order Comfirm</Link>} position="top left">
                                {close => (
                                    <div>
                                        <a className="close" onClick={close}>
                                            &times;
                                        </a>
                                        <label for="size_1">Order Confirmation <em>*</em></label>
                                        <select name="status" className="form-control " value={status} onChange={(e) => { Setstatus(e.target.value) }}>
                                            <option >Select</option>
                                            <option value="Yes"> Yes</option>
                                            <option value="No">No</option>
                                        </select>
                                        <br />
                                        <Link type="submit" className="btn btn-success" onClick={senddata}>Save </Link>
                                    </div>
                                )}
                            </Popup>    </div>
                        </div>
                    </div>)

                }

                )

                }
            </Col>
        </Row>

    );
}






export default Login;