
import Table from 'react-bootstrap/Table';
import React, { useRef, useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import Popup from 'reactjs-popup';
function PolicyInfo(props) {
    const [Users, SetUser] = useState([""]);
    const [product_name, setproduct_name] = useState('');
    const [product_type, setproduct_type] = useState('');
    const [manufacturer, setmanufacturer] = useState('');
    const [scm1, setscm1] = useState('');
    const [hsncode, sethsncode] = useState('');
    const [pack, setpack] = useState('');
    const [mrp, setmrp] = useState('');
    const [qty, setqty] = useState([]);
    const [rate, setrate] = useState('');
    const [cgst, setcgst] = useState('');
    const [sgst, setsgst] = useState('');
    const [scm2, setscm2] = useState('');
    const [id, setid] = useState('');
    var totalamount = 0;
    var totalqty = 0;
    useEffect(() => {
        getlist()
    }, []);

    function getlist() {
        fetch("http://192.168.1.7:4000/order/", {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
            },
        })
            .then((result) => {
                result.json()
                    .then((data) => {
                        SetUser(data.list)
                        console.log("data", data.list);


                    })
            })
    }
    function deleteUser(id) {
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
        fetch(`http://192.168.1.7:4000/order/${id}`, {
            method: 'DELETE',
            headers: {
                'Accept': 'application/json',
                'Content-type': 'application/json',
                'Authorization': user_data.jwt,

            },
        }).then((result) => {
            result.json().then((resp) => {

                console.log("resp", resp)
                getlist()
            }

            )
        })
    }
    const saveUser = (items,id) => {
        console.log("items", id)
        if (items) {
            setid(items.id)
            console.log("items", items.id)
            setproduct_name(items.product_name)
            console.log("items", items.product_name)
            setproduct_type(items.product_type)
            setmanufacturer(items.manufacturer)
            setscm1(items.scm1)
            setscm2(items.scm2)
            sethsncode(items.hsncode)
            setpack(items.pack)
            setmrp(items.mrp)
            setcgst(items.cgst)
            setqty(items.qty)
            setrate(items.rate)
            setsgst(items.sgst)
        }
    };
    const senddata = (id) => {
        console.log(id, product_type, product_name, scm1, hsncode, pack, mrp, rate, cgst, sgst, scm2, qty, manufacturer)
        let date = { id, product_type, product_name, scm1, hsncode, pack, mrp, rate, cgst, sgst, scm2, qty, manufacturer }
        fetch(`http://192.168.1.7:4000/order/${id}`, {
            method: 'PUT',
            headers: {
                'Accept': 'application/json',
                'Content-type': 'application/json',
            },
            body: JSON.stringify(date)

        })
            .then(response => response.json())
            .then((resData) => {
                console.log(resData)
                alert("Qty Updated Successfully")
            }
            )
    }
    return (
        <Table striped bordered hover >
            <thead>
                <tr>
                    {/* <th><span>SR/No</span></th> */}
                    <th><span>PRODUCT NAME</span></th>
                    <th><span>QTY</span></th>
                    <th><span>MANUFACTURE</span></th>
                    <th><sapn>HSN CODE</sapn></th>
                    <th><span>SCM 1</span></th>
                    <th><span>SCM 2</span></th>
                    <th><span>PACK</span></th>
                    <th><span>MRP</span></th>
                    <th><span>RATE</span></th>
                    <th><span>AMOUNT</span></th>
                    <th><span>ACTION</span></th>

                </tr>
            </thead>
            {

                Users && Users.map((items, index) => {
                    totalamount += items.amount;
                    totalqty += items.qty

                    return (
                        <tr>
                            {/* <td class="ty-09">
                        <p class="tk-10"><span className="details"></span>{totalamount}</p>
                    </td> */}
                            <td class="ty-09">
                                <span class="label label-default">{items.product_name}</span>
                            </td>
                            <td class="ty-09">
                                <p class="label label-default">{items.qty}</p>
                            </td>
                            <td class="ty-09">
                                <p class="label label-default">{items.manufacturer}</p>
                            </td>
                            <td class="ty-09">
                                <p class="label label-default">{items.hsncode}</p>
                            </td>
                            <td class="ty-09">
                                <p class="label label-default">{items.scm1}</p>
                            </td>
                            <td class="ty-09">
                                <p class="label label-default">{items.scm2}</p>
                            </td>
                            <td class="ty-09">
                                <p class="label label-default">{items.pack}</p>
                            </td>
                            <td class="ty-09">
                                <p class="label label-default">{items.mrp}</p>
                            </td>

                            <td class="ty-09">
                                <span class="label label-default">{items.rate}</span>
                            </td>
                            <td> <span className="details1">{items.amount} </span>
                            </td>
                            <td >

                                <Popup trigger={<Link onClick={() => saveUser(items)} class="table-link text-info">

                                    <span class="fa-stack myDIV">
                                        <i class="fa fa-square fa-stack-2x"></i>
                                        <i class="fa fa-pencil fa-stack-1x fa-inverse"></i>
                                    </span>

                                </Link>} position="top left">
                                    {close => (
                                        <div>
                                            <a className="close" onClick={close}>

                                                &times;
                                            </a>

                                            <p> Qty </p>
                                            <input type="number" className="form-control form-control-lg" name="qty" value={items.qty} onChange={(e) => setqty(e.target.value)} />

                                            <br />
                                            <Link type="submit" className="btn btn-success" onClick={senddata} key={items.id}>Save </Link>


                                        </div>
                                    )}
                                </Popup>
                                <a class="table-link " href="#" onClick={() => deleteUser(items.id)}>
                                    <span class="fa-stack">
                                        <i class="fa fa-square fa-stack-2x"></i>
                                        <i class="fa fa-trash-o fa-stack-1x fa-inverse"></i>
                                    </span>
                                </a>
                            </td>

                        </tr>
                    )

                }

                )

            }
        </Table>

    );
}






export default PolicyInfo;