import Row from 'react-bootstrap/Row';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

import { Link } from 'react-router-dom';

import jwt_decode from "jwt-decode";

class Header extends Component {
    constructor(props) {
        super(props)
        this.state = {
            SearchData: {},


        }
    }

    componentDidMount() {
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
        const decoded = jwt_decode(user_data.jwt);
        console.log("decoded", decoded);



    }

    LogoutUser(key) {
        console.warn(key)
        fetch("https://connexialfintech.pythonanywhere.com/logout", {
            method: 'POST',

            headers: {
                'Accept': 'application/json',
                'Content-type': 'application/json',

            },
            body: JSON.stringify()

        })
            .then(response => response.json())
            .then((responsejson) => {
                console.log("rajevv", responsejson);
                localStorage.clear()
            })

    }
    render() {
        const token = JSON.parse(localStorage.getItem('myData'));
        const decoded = jwt_decode(token.jwt);

        return (

            <nav class="navbar navbar-expand-lg navbar-light bg-light1 fixed-top ">
                <div className="row">
                

                    <button class="navbar-toggler navbar-toggler-right border-0" type="button" data-toggle="collapse" data-target="#navbar4">
                        <span class="navbar-toggler-icon"></span>
                    </button>


                    <div class="collapse navbar-collapse " id="navbar4">
                    <a class="navbar-brand d-flex align-items-center" onClick={() => window.open("someLink")}>
                        <a class="nav-tz" onClick={() => window.open("/Order","_self")}>  <img src="/helthcare.png" alt="logo" className="logo-img" /></a>
                    </a>
                        <ul class="navbar-nav mr-auto ">
                          
                            <li class="nav-item "> <a class="nav-link" href="#" onClick={() => window.open("/Order", "_self")}><span class="d-inline-block d-lg-none icon-width"><i class="fa fa-spa"></i></span> Order</a> </li>
                            <li class="nav-item "> <a class="nav-link" href="#" onClick={() => window.open("/Orderstatus", "_self")}><span class="d-inline-block d-lg-none icon-width"><i class="fa fa-spa"></i></span> Order Status</a> </li>
                            <li class="nav-item "> <a class="nav-link" href="#" onClick={() => window.open("./sales", "_self")}><span class="d-inline-block d-lg-none icon-width"><i class="fa fa-spa"></i></span> Invoice</a> </li>
                            <li class="nav-item "> <a class="nav-link" href="#" onClick={() => window.open("./Report", "_self")}><span class="d-inline-block d-lg-none icon-width"><i class="fa fa-spa"></i></span> Report</a> </li>
                            
                            <li class="nav-item "> <a class="nav-link" href="#" onClick={() => window.open("/Payment", "_self")}><span class="d-inline-block d-lg-none icon-width"><i class="fa fa-spa"></i></span> Online Payment</a> </li>



                        </ul>


                        <ul class="navbar-nav ml-auto mt-3 mt-lg-0">
                            <h5 className="company-name"><span>Welcome  </span>{decoded.fullname}</h5>
                            <li><span >Last Login</span><span > : {decoded.last_login} </span><span > | </span></li>


                            <li class="nav-item  dropdown d-menu">
                                <a class="nav-link dropdown-toggle rt6" href="#" id="dropdown01" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"><span class="d-inline-block d-lg-none icon-width"><i class="far fa-caret-square-down"></i></span>Profile : {decoded.user_type}
                                </a>
                                <div class="dropdown-menu shadow-sm sm-menu" aria-labelledby="dropdown01">
                                    {/* <a class="dropdown-item" href="#" onClick={() => window.open("./profile", "_self")} >Profile</a> */}
                                    {
                                        localStorage.getItem('login') ?
                                            <a to="/login" class="dropdown-item" >Login</a>
                                            :

                                            <Link to="/login" onClick={(e) => this.LogoutUser(e)} class="dropdown-item" >Logout</Link>
                                    }
                                </div>
                            </li>
                        </ul>

                    </div>
                </div>

            </nav>


        );
    }
}
export default Header

