
import Header from "./Header";
import React from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Table from 'react-bootstrap/Table';

class Orderstatus extends React.Component {

    constructor() {
        super()
        this.state = {
            SearchData: null,


        }
    }


    SearchInsured(key) {
        
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
        console.warn(key)
        fetch(`https://connexialfintech.pythonanywhere.com/adminclientlistfilter/?search=` + key, {
            method: 'GET',
            headers: {

                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Authorization': user_data.jwt,
            },
        }).then((data) => {
            data.json().then((resp) => {
                console.warn("resp", resp)
                this.setState({ SearchData: resp })

            })
        })

    }
    SearchInsurance(key) {
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
        console.warn(key)
        fetch(`https://connexialfintech.pythonanywhere.com/adminclientlistfilter/?insurance=` + key, {
            method: 'GET',
            headers: {

                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Authorization': user_data.jwt,
            },
        }).then((data) => {
            data.json().then((resp) => {
                console.warn("resp", resp)
                this.setState({ SearchData: resp })

            })
        })

    }

    SearchDateto(key) {
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
        console.warn(key)
        fetch(`http://192.168.1.6:4000/date_filter/?Date_to=` + key, {
            method: 'GET',
            headers: {

                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Authorization': user_data.jwt,
            },
        }).then((data) => {
            data.json().then((resp) => {
                console.warn("resp", resp)
                this.setState({ SearchData: resp })

            })
        })

    }
    SearchDatefrom(key) {
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
        console.warn(key)
        fetch(`http://192.168.1.6:4000/date_filter/?Date_to_gte=` + key, {
            method: 'GET',
            headers: {

                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Authorization': user_data.jwt,
            },
        }).then((data) => {
            data.json().then((resp) => {
                console.warn("resp", resp)
                this.setState({ SearchData: resp })

            })
        })

    }

    deleteUser(id) {
        fetch(`
    https://connexialfintech.pythonanywhere.com/policy/clients/${id}`, {
            method: 'DELETE',
            headers: {
                'Accept': 'application/json',
                'Content-type': 'application/json',
                "Authorization": 'auth',
            },

        }).then((result) => {
            result.json().then((resp) => {
                console.warn(resp)
                this.setState({ SearchData: null })

            })
        })
    }

    render() {

        return (
            <div>
                <Header />
                <br></br>
                <br></br>
                <br></br>
                <br></br>
                <div id="msform">

                    <div className="fstep">
                        <fieldset>
                            <div class="row">
                                <div class="col-lg-6 col-md-6">
                                    <h6>Order Status</h6>
                                </div>
                                <div class="col-lg-6 col-md-6">
                                    <button className="refresh" onClick={() => window.location.reload(false)}>Refresh</button>
                                </div>
                            </div>

                            <hr></hr>
                            <form>

                                <Row>

                                    <Col xs={12} md={2} lg={2} >
                                        <label for="size_1">Search</label>
                                        <input type="text" onChange={(event) => this.SearchInsured(event.target.value)} name="Insured_Name" />
                                    </Col>


                                    <Col xs={12} md={2} lg={2} >
                                        <label for="size_1">Cust Order No</label>
                                        <input type="text" onChange={(event) => this.SearchInsurance(event.target.value)} name="Insurance" />
                                    </Col>


                                    <Col xs={12} md={2} lg={2} >
                                        <label for="size_1">Vendor Order No</label>
                                        <input type="text" onChange={(event) => this.SearchRisk_Note(event.target.value)} name="Vendor Order No" />
                                    </Col>
                                   
                                    <Col xs={12} md={2} lg={2} >
                                        <label for="size_1">Status <em>*</em></label>
                                        <select name="status" onChange={(event) => this.Searchstatus(event.target.value)} >
                                            <option >Select</option>
                                            <option value="Approved"> Approved</option>
                                            <option value="Pending">Pending</option>
                                            <option value="Cancelled">Cancelled</option>

                                        </select>

                                    </Col>
                                    <Col xs={12} md={2} lg={2} >
                                        <label for="size_1">Date From</label>
                                        <input type="date" onChange={(event) => this.SearchDatefrom(event.target.value)} name="Date From" />
                                    </Col>
                                    <Col xs={12} md={2} lg={2} >
                                        <label for="size_1">Date To</label>
                                        <input type="date" onChange={(event) => this.SearchDateto(event.target.value)} name="Date To" />
                                    </Col>
                                </Row>
                            </form>
                        </fieldset>



                    </div>

                    <div className="fstep">

                        {
                            this.state.SearchData ?
                                <Table striped bordered hover >


                                    <thead>
                                        <tr>

                                            <th><span>Sr/No.</span></th>
                                            <th><span>Vendor Order No.</span></th>
                                            <th><span>Cust Order No.</span></th>
                                            <th ><span>Order Date</span></th>
                                            <th><span>Total</span></th>
                                            <th><span>Status</span></th>
                                            <th><span>Action</span></th>

                                        </tr>
                                    </thead>



                                    {
                                        this.state.SearchData.map((items) =>

                                           
                                                <tr>
                                                    <td class="ty-09">
                                                        <p class="tk-10"><span className="details">{items.client}</span></p>
                                                    </td>

                                                    <td class="ty-09">
                                                        <span class="label label-default">{items.client_name}</span>
                                                    </td>
                                                    <td class=" ty-09">
                                                        <span className="details1">{items.insured_name}</span>
                                                    </td>
                                                    <td class="ty-09">
                                                        <span class="label label-default">{items.insurer}</span>
                                                    </td>


                                                    <td> <span className="details1">{items.date_from} </span>
                                                    </td>
                                                    <td class="ty-09">
                                                        <span class="label label-default">{items.client}</span>
                                                    </td>
                                                    <td class="ty-09">
                                                        <span class="label label-default">{items.client}</span>
                                                    </td>
                                                    
                                                   

                                                </tr>
                                          


                                        )

                                    }
                                </Table>
                                : <Table striped bordered hover >


                                    <thead>
                                        <tr className="notfound">
                                            <tr>
                                                <p>Data Don't Show ( Please Shearch )</p>
                                            </tr>

                                        </tr>
                                    </thead>
                                </Table>


                        }










                    </div>
                </div>
            </div >
        )
    }

}

export default Orderstatus;