

    import * as React from "react";

    import ReactApexChart from 'react-apexcharts'
    import axios from 'axios'
    export default class AreaChart extends React.Component {
      constructor(props) {
        super(props);
          
     
        this.state = {

          series: [{
            name: 'Sales',
            type: 'column',
            data: []
          }, {
            name: 'Target',
            type: 'line',
            data: []
          }],
          
          options: {
            chart: {
              width: 380,
              type: 'line',
            },
            stroke: {
              width: [0, 4]
            },
    
            dataLabels: {
              enabled: true,
              enabledOnSeries: [1]
            },
            labels: ['01 Jan 2021', '02 Feb 2021', '03 March 2021', '04 Jun 2021', '05 July 2021', '06 Aug 2021', '07 Spt 2021', '08 Oct 2021', '09 Nov 2021', '10 Dec 2021'],
    
            yaxis: [{
              title: {
    
              },
    
            }, {
              opposite: true,
              title: {
    
              }
            }]
          },
      }
      }
       componentDidMount() {
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
        const sales = []; const target = [];
         axios.get("https://connexialfintech.pythonanywhere.com/policy/AdminSalesTargetViewSet/",{
          method: 'GET',
          headers: {

              'Accept': 'application/json',
              'Content-Type': 'application/json',
              'Authorization': user_data.jwt,
          },
      })

          .then(Response => {
            console.log("Response", Response.data);
            for (const obj of Response.data) {
              console.log("obj", obj);
              sales.push(obj.sales);
              target.push(obj.target ? obj.target.target__sum :0);

            }
            console.log("sales,target", sales, target);

            this.setState({


              series: [{
                name: 'Sales',
                type: 'column',
                data: sales
              }, {
                name: 'Target',
                type: 'line',
                data: target
              }],

            })

          })
          .catch(err => {
            console.log("errr", err);
          })
 
      }
      render() {
        return (
          <div id="chart">
      <ReactApexChart options={this.state.options} series={this.state.series} type="line" />
       
              </div>
        )
      }
    }