
import Header from "../Header";
import { Link } from 'react-router-dom';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Button } from 'react-bootstrap';
import React, { useRef, useState, useEffect } from "react";
import axios from "axios";
import { useHistory, useParams } from "react-router-dom";
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import ReactToPrint from 'react-to-print';

const Qtyupdate = () => {
    let history = useHistory();
    const { id } = useParams();
    const componentRef1 = useRef();

    const [User] = useState({})

    const [user, setUser] = useState({
        product_name: "", product_type: "", manufacturer: "", scm1: "", hsncode: "", pack: "", mrp: "", qty: "", rate: "", cgst: "", sgst: "", scm2: "", 

    });

    const {product_name,product_type,manufacturer, scm1,hsncode,pack,mrp,qty,rate,cgst,sgst,scm2,} = user;
    const onInputChange = e => {
        setUser({ ...user, [e.target.name]: e.target.value });
    };

    useEffect(() => {
        loadUser();
    }, []);

    const onSubmit = async e => {

        await axios.put(`http://192.168.1.6:4000/order/${id}/`, user);
        console.log(user.data)

    };

    const loadUser = async () => {
        const result = await axios.get(`http://192.168.1.6:4000/order/${id}/`);
        setUser(result.data);
        setUser(result.data);
        console.log(result.data)
    };


    return (
        <div id="work-info" class="row">


            <input type="text" className="form-control form-control-lg" name="client_name" value={qty} />




            <Row>

                <Col xs={12} md={10} lg={10} ></Col>
                <Col xs={12} md={1} lg={1} >
                    <Button className="btn btn-danger Insert2" onClick={() => window.open("/Admin/Dashboard", "_self")}>Cancel</Button>
                </Col>
                <Col xs={12} md={1} lg={1} >
                    <Link type="submit" className="btn save-btn Insurt " onClick={() => onSubmit()} >Save</Link>
                </Col>
            </Row>
        </div >
    )

}
export default Qtyupdate;