
import { Button } from 'react-bootstrap';
import Table from 'react-bootstrap/Table';
import Header from "./Header";
import React from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Footer from './Footer';
import { useHistory, useParams } from "react-router-dom";
import { useState, useEffect } from 'react'
import swal from 'sweetalert'

import jwt_decode from "jwt-decode";
const Order = (props) => {
  const [SearchData, setSearchData] = useState([])
  const [product_name, setproduct_name] = useState('');
  const [product_type, setproduct_type] = useState('');
  const [manufacturer, setmanufacturer] = useState('');
  const [scm1, setscm1] = useState('');
  const [hsncode, sethsncode] = useState('');
  const [pack, setpack] = useState('');
  const [mrp, setmrp] = useState('');
  const [qty, setqty] = useState([]);
  const [rate, setrate] = useState('');
  const [cgst, setcgst] = useState('');
  const [sgst, setsgst] = useState('');
  const [scm2, setscm2] = useState('');
  const [id, setid] = useState('');
  const [Users, SetUser] = useState([""]);

  let history = useHistory();

  var totalamount = 0.00;
  var totalqty = 0.00;
  const SearchInsured = (key) => {

    if (key.trim().length > 0) {
      var Userdetials = localStorage.getItem('myData')
      var user_data = JSON.parse(Userdetials)
      console.warn(key)
      fetch(`http://192.168.1.9:4000/productsearch/?search=` + key, {
        method: 'GET',
        headers: {

          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'Authorization': user_data.jwt,
        },
      }).then((data) => {
        data.json().then((resp) => {

          console.warn("jgdsghfjhf", resp)
          setSearchData(resp)

        })
      })
    }
    else {
      setSearchData([])
    }
  }
  const saveUser = (items) => {
    console.log("items", items)
    if (items) {
      setproduct_name(items.product_name)
      setproduct_type(items.product_type)
      setmanufacturer(items.manufacturer)
      setscm1(items.scm1)
      setscm2(items.scm2)
      sethsncode(items.hsncode)
      setpack(items.pack)
      setmrp(items.mrp)
      setcgst(items.cgst)
      setqty(items.qty)
      setid(items.id)
      setrate(items.rate)
      setsgst(items.sgst)
    }
  };
  const senddata = () => {
    console.log(product_type, product_name, scm1, hsncode, pack, mrp, rate, cgst, sgst, scm2, qty, manufacturer)
    let date = { product_type, product_name, scm1, hsncode, pack, mrp, rate, cgst, sgst, scm2, qty, manufacturer }
    fetch("http://192.168.1.9:4000/order/", {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-type': 'application/json',
      },
      body: JSON.stringify(date)

    })
      .then(response => response.json())
      .then((resData) => {
        console.log(resData)
        // props.history.push('./Logout');
      }
      )
  }

  useEffect(() => {
    getlist()
  }, []);

  function getlist() {
    var Userdetials = localStorage.getItem('myData')
    var user_data = JSON.parse(Userdetials)
    fetch("http://192.168.1.9:4000/order/", {
      method: 'GET',
      headers: {
        'Accept': 'application/json',
        'Authorization': user_data.jwt,

      },
    })
      .then((result) => {
        result.json()
          .then((data) => {
            SetUser(data.list)
            console.log("datasdwdw", data.list);


          })
      })
  }
  function deleteUser(id) {
    var Userdetials = localStorage.getItem('myData')
    var user_data = JSON.parse(Userdetials)
    fetch(`http://192.168.1.9:4000/order/${id}`, {
      method: 'DELETE',
      headers: {
        'Accept': 'application/json',
        'Content-type': 'application/json',
        'Authorization': user_data.jwt,

      },
    }).then((result) => {
      result.json().then((resp) => {

        console.log("resp", resp)
        getlist()
      }

      )
    })
  }
  function onSubmit() {
    console.log("hbbhj", id)
    if (id) {
      console.log(product_type, product_name, scm1, hsncode, pack, mrp, rate, cgst, sgst, scm2, qty, manufacturer)
      let date = { product_type, product_name, scm1, hsncode, pack, mrp, rate, cgst, sgst, scm2, qty, manufacturer }

      fetch(`http://192.168.1.9:4000/order/${id}/`, {
        method: 'PUT',
        headers: {
          'Accept': 'application/json',
          'Content-type': 'application/json',

        },
        body: JSON.stringify(date)

      })
        .then(response => response.json())
        .then((resData) => {
          console.log(resData)

          if (resData.message == "Your order is palced Successfully") {


            return swal("Thank You!", "Your Order Palced Successfully!", "success");


          }
          else
            return history.push('/admin/Logout');
        }
        )

    }

  };


  return (
    <div>


      <Header />
      <br></br>
      <br></br>
      <br></br>
      <br></br>

      <div id="msform">

        <div className="fstep">
          <fieldset>

            <form >

              <Row>

                <Col xs={12} md={2} lg={2} >
                  <p> Search Product </p>
                  <input type="text" onChange={(event) => SearchInsured(event.target.value)} name="Insured_Name" />
                </Col>

                <Col xs={12} md={10} lg={10} >
                  <from id="msform"  >
                    <Row>
                      <Col xs={12} md={2} lg={2} >
                        <p>Product Name </p>
                        <input type="text" className="form-control form-control-lg" name="product_name" value={product_name} /> </Col>
                      <Col xs={12} md={2} lg={2} >
                        <p>Product Type</p>
                        <input type="text" className="form-control form-control-lg" name="product_type" value={product_type} />
                      </Col>
                      <Col xs={12} md={2} lg={2} >
                        <p> Manufacturer </p>
                        <input type="text" className="form-control form-control-lg" name="manufacturer" value={manufacturer} />
                      </Col>
                      <Col xs={12} md={2} lg={2} >
                        <p> HSN Code </p>
                        <input type="text" className="form-control form-control-lg" name="hsncode" value={hsncode} />
                      </Col>

                      <Col xs={12} md={1} lg={1} >
                        <p> Oty </p>
                        <input type="number" className="form-control form-control-lg" name="qty" value={qty} onChange={(e) => setqty(e.target.value)} />
                      </Col>

                      <Col xs={12} md={2} lg={2} >
                        <p> Scm </p>
                        <input type="text" className="form-control form-control-lg" name="scm1" value={scm1} />
                      </Col>
                      <Col xs={12} md={1} lg={1} >
                        <p> MRP </p>
                        <input type="text" className="form-control form-control-lg" name="mrp" value={mrp} />
                      </Col>

                      <Col xs={12} md={10} lg={10} >

                      </Col>
                      <Col xs={12} md={1} lg={1} >
                        <Button className="btn btn-danger Insert2" onClick={() => window.open("/admin/Order", "_self")}>Reset</Button>
                      </Col>
                      <Col xs={12} md={1} lg={1} >
                        <Button type="submit" id="savedata" className="btn save-btn  Insurt " onClick={senddata} >Insurt</Button>

                      </Col>

                    </Row>

                  </from >
                </Col>

              </Row>
            </form>
          </fieldset>



        </div>


        {
          SearchData.length > 0 ?

            <div className=" list">

              {SearchData && SearchData.map((items, key) => {

                return (

                  <a onClick={() => saveUser(items)} class="dropdown-item" href="#" key={items.id} >
                    {items.product_name} </a>
                )
              }


              )

              }
            </div>
            : null

        }
      </div>
      <div className="fstep">
        {
          SearchData ?
            <Table striped bordered hover >
              <thead>
                <tr>
                  {/* <th><span>SR/No</span></th> */}
                  <th><span>PRODUCT NAME</span></th>
                  <th><span>QTY</span></th>
                  <th><span>MANUFACTURE</span></th>
                  <th><sapn>HSN CODE</sapn></th>
                  <th><span>SCM 1</span></th>
                  <th><span>SCM 2</span></th>
                  <th><span>PACK</span></th>
                  <th><span>MRP</span></th>
                  <th><span>RATE</span></th>
                  <th><span>AMOUNT</span></th>
                  <th><span>ACTION</span></th>

                </tr>
              </thead>
              {

                Users && Users.map((items, index) => {
                  totalamount += items.amount;
                  totalqty += items.qty;

                  //  setqty(items.qty);

                  return (
                    <tr>
                      {/* <td class="ty-09">
                        <p class="tk-10"><span className="details"></span>{totalamount}</p>
                    </td> */}
                      <td class="ty-09">
                        <span class="label label-default">{items.product_name}</span>
                      </td>
                      <td class="ty-09">
                        <p class="label label-default">{items.qty}</p>

                      </td>
                      <td class="ty-09">
                        <p class="label label-default">{items.manufacturer}</p>
                      </td>
                      <td class="ty-09">
                        <p class="label label-default">{items.hsncode}</p>
                      </td>
                      <td class="ty-09">
                        <p class="label label-default">{items.scm1}</p>
                      </td>
                      <td class="ty-09">
                        <p class="label label-default">{items.scm2}</p>
                      </td>
                      <td class="ty-09">
                        <p class="label label-default">{items.pack}</p>
                      </td>
                      <td class="ty-09">
                        <p class="label label-default">{items.mrp}</p>
                      </td>

                      <td class="ty-09">
                        <span class="label label-default">{items.rate}</span>
                      </td>
                      <td> <span className="details1">{items.amount} </span>
                      </td>
                      <td >


                        <div id="popup1" class="overlay">
                          <div class="popup">
                            <h6>Update Quantity</h6>
                            <a class="close" href="#">&times;</a>
                            <hr/>
                            <div class="content">
                              <input type="number" className="form-control form-control-lg" name="qty" value={qty} onChange={(e) => setqty(e.target.value)} />
                              <Button type="submit" id="savedata" className="btn save-btn  Insurt " onClick={() => onSubmit(items.id)} >Insurt</Button>
                            </div>
                          </div>
                        </div>
                        <a href="#popup1" onClick={() => saveUser(items)} class="table-link text-info" >

                          <span class="fa-stack myDIV" id="edit">
                            <i class="fa fa-square fa-stack-2x"></i>
                            <i class="fa fa-pencil fa-stack-1x fa-inverse"></i>
                          </span>

                        </a>
                        <a class="table-link " href="#" onClick={() => deleteUser(items.id)}>
                          <span class="fa-stack">
                            <i class="fa fa-square fa-stack-2x"></i>
                            <i class="fa fa-trash-o fa-stack-1x fa-inverse"></i>
                          </span>
                        </a>
                      </td>

                    </tr>
                  )

                }

                )

              }
            </Table>
            : null
        }

      </div>
      <Footer />
    </div >
  )
}



export default Order;




