import React from 'react';
import Row from 'react-bootstrap/Row';
import { Link } from 'react-router-dom';
import Col from 'react-bootstrap/Col';
import react, { useState } from 'react';
import axios from 'axios';
import { Button } from 'react-bootstrap';
function Add_User(props) {
    const [fullname, setfullname] = useState('');
    const [mobile_no, setmobile_no] = useState('');
    const [email, setemail] = useState('');
    const [password, setpassword] = useState('');
    const [gst_no, setgst_no] = useState('')
    const [drug_lic_no, setdrug_lic_no] = useState('')
    const [tel_no, settel_no] = useState('');
    const [business_name, setbusiness_name] = useState('');
    const [business_address, setbusiness_address] = useState('');
    const [business_logo, setbusiness_logo] = useState(null);
    const handleImageChange = (file) => {
        // file_upload: e.target.files[0]
        setbusiness_logo(file.target.files[0])
    }
    const senddata = () => {

        let form_data = new FormData();
        if (business_logo == null) {
            form_data.append('business_logo', "");
        } else {
            form_data.append('business_logo', business_logo, business_logo.name);
        }
        console.log("skjfhdh", form_data)
        form_data.append('password', password);
        form_data.append('email', email);
        form_data.append('drug_lic_no', drug_lic_no);
        form_data.append('gst_no', gst_no);
        form_data.append('fullname', fullname);
        form_data.append('mobile_no', mobile_no);
        form_data.append('tel_no', tel_no);
        form_data.append('business_name', business_name);
        console.log("form_data", form_data)
        let url = 'http://192.168.1.3:4000/api/register/';
        axios.post(url, form_data, {
            headers: {
                'content-type': 'multipart/form-data',

            }
        })
            .then(response => {
                console.log("rajevvresponsejson", response);
                props.history.push('/login');
                if (response.status == 200) {
                    return (
                        alert("Your Registration Is Successfully")
                    );

                }
                else {
                    alert('Something Error');
                }

            })
        // .catch(err => console.log(err))
    };
    return (

        <form id="msform ">
     
            <div class=" h-100">
                <div class="d-flex justify-content-center h-100">
                    <div class="Ragistarstion">
                        <div class="d-flex justify-content-center">
                            <div class="brand_logo_container">
                                <img src="logo-01.png" class="brand_logo" alt="Logo" />
                            </div>
                        </div>
                        <div class="d-flex justify-content-center form_container Registration">
                            <form >
                            <Row>
                         
                                    <Col xs={12} md={12} lg={12} >
                                        <h6>Registration</h6>
                                        <hr></hr>
                                    </Col>

                                    <Col xs={12} md={6} lg={6} >
                                        <label for="size_1">Full Name<em>*</em></label>
                                        <input type="text" class="form-control" value={fullname} onChange={(e) => { setfullname(e.target.value) }} name="fullname" />
                                    </Col>

                                    <Col xs={12} md={6} lg={6} >
                                        <label for="size_1">Mobile No <em>*</em></label>
                                        <input type="number" class="form-control" value={mobile_no} onChange={(e) => { setmobile_no(e.target.value) }} name="mobile_no" />
                                    </Col>
                                    <Col xs={12} md={6} lg={6} >
                                        <label for="size_1">GST No</label>
                                        <input type="email" class="form-control" value={gst_no} onChange={(e) => { setgst_no(e.target.value) }} name="gst_no" />


                                    </Col>
                                    <Col xs={12} md={6} lg={6} >
                                        <label for="size_1">Drug Licence No<em>*</em></label>
                                        <input type="email" class="form-control" value={drug_lic_no} onChange={(e) => { setdrug_lic_no(e.target.value) }} name="drug_lic_no" />


                                    </Col>
                                    <Col xs={12} md={6} lg={6} >
                                        <label for="size_1">Email Id<em>*</em></label>
                                        <input type="email" class="form-control" value={email} onChange={(e) => { setemail(e.target.value) }} name="email" />

                                    </Col>

                                    <Col xs={12} md={6} lg={6} >
                                        <label for="size_1">Telephone No</label>
                                        <input type="text" class="form-control" value={tel_no} onChange={(e) => { settel_no(e.target.value) }} name="tel_no" />
                                    </Col>

                                    <Col xs={12} md={6} lg={6} >
                                        <label for="size_1"> Business Name  <em>*</em> </label>
                                        <input type="text" class="form-control" value={business_name} onChange={(e) => { setbusiness_name(e.target.value) }} name="business_name" />
                                    </Col>
                                    <Col xs={12} md={6} lg={6} >
                                        <label for="size_1">Business Logo</label>
                                        <input type="file" class="form-control" accept="file_upload/png, file_upload/jpeg" onChange={handleImageChange} name="company_logo" />

                                    </Col>
                                    <Col xs={12} md={6} lg={6} >
                                        <label for="size_1">Business Address  <em>*</em></label>
                                        <input type="text" class="form-control" value={business_address} onChange={(e) => { setbusiness_address(e.target.value) }} name="business_address" />
                                    </Col>


                                    <Col xs={12} md={6} lg={6} >
                                        <label for="size_1">Password<em>*</em> </label>
                                        <input type="text" class="form-control" value={password} onChange={(e) => { setpassword(e.target.value) }} name="password" />
                                    </Col>


                                    <Col xs={12} md={12} lg={12} ></Col>
                                    <br></br>
                                    <Col xs={12} md={2} lg={2} >

                                    </Col>
                                    <Col  xs={5} md={2} lg={2} >
                                        <Button className="btn btn-danger " onClick={() => window.open("/", "_self")} >Cancel</Button>
                                    </Col>
                                    <Col xs={1} md={4} lg={4} className="rty" >


                                    </Col>
                                    <Col xs={6} md={4} lg={4} >
                                        <Link type="submit" className="btn save-btn Insurt " onClick={senddata}>Save </Link>
                                    </Col>
                                </Row>


                            
                            </form>
                        </div>

                        <div class="mt-4">
                            <div class="d-flex justify-content-center links online_order">
                                Don't have an account? <a href="#"  onClick={() => window.open("/login", "_self")} class="ml-2 online_order">Sign In</a>
                            </div>
                            <div class="d-flex justify-content-center links online_order">
                                <a href="#" class=" online_order">Forgot your password?</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </form>

    )
}


export default Add_User;