import * as React from "react";

import Chart from 'react-apexcharts'
import axios from 'axios'
export default class Bar extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      options: {
        chart: {
          id: 'apexchart-example'
        },
        xaxis: {
          categories: []
        }
      },
      series: [{
        name: 'Leads',
        data: []
      },
      {
        name: 'Sales',
        data: []
      },
      {
        name: 'cancelled',
        data:  []
      },
      {
        name: 'Pending',
        data:  []
      },
    
    ]
      
    }
  }
  async componentDidMount (){
    var Userdetials = localStorage.getItem('myData')
    var user_data = JSON.parse(Userdetials)
    const Pending = [];const Approved = [];const Cancelled = [];
    await axios.get("https://connexialfintech.pythonanywhere.com/policy/AdminLeadPerfomnceViewSet/",{
      method: 'GET',
      headers: {

          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'Authorization': user_data.jwt,
      },
  })
    .then(Response=> {
      console.log("Response", Response.data);
      for(const obj of Response.data) 
      {
        console.log("obj",obj);
        Pending.push(obj.pending);
        Approved.push(obj.approved);
        Cancelled.push(obj.cencel);
      }
      console.log("Pending,Approved,Cancelled",Pending,Approved,Cancelled);
    
      this.setState({
        options: {
          chart: {
            id: 'apexchart-example'
          },
        },
        series: [{
          name: 'Approved',
          data: Approved,
         
        },
        {
          name: 'Pending',
          data: Pending,
        },
        {
          name: 'Cancelled',
          data: Cancelled,
        },
        
      
      ]
      })
    })
    .catch(err=>{
      console.log("errr", err);
    })
  }
  render() {
    return (
      <Chart options={this.state.options} series={this.state.series} type="bar"  />
    )
  }
}