
import Table from 'react-bootstrap/Table';
import React, { useState, useEffect } from 'react';
import { useHistory} from "react-router-dom";
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Popup from 'reactjs-popup';
import swal from 'sweetalert'
function Footer(props) {
    const [Users, SetUser] = useState([""]);
  
    const [status, Setstatus] = useState('');
    var totalamount = 0;
    var totalqty = 0;
    var totalamountvat = [''];

    let history = useHistory();

    useEffect(() => {
        getlist()
    }, []);
    // var  Spinner =  true;
    function getlist() {
        fetch("http://192.168.1.9:4000/order/", {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
            },
        })
            .then((result) => {
                result.json()
                    .then((data) => {
                        SetUser(data.list)
                        
                        console.log("data515458454", data.list);


                    })
            })
    }

    const senddata = (props) => {
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
        console.log(totalamount, totalqty,Users )
        let date = {totalamount,totalqty,Users }
        fetch("http://192.168.1.9:4000/confirm/", {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-type': 'application/json',
                'Authorization': user_data.jwt,
            },
            body: JSON.stringify(date)

        })
            .then(response => response.json())
            .then((resData) => {
                console.log(resData)
                history.push('/admin/Logout');
                if (resData.message === 'update reacord successfuly') {
                    console.log("sbaasjhasdhj",resData.message )
                    
                    swal("Thank You!", "Your Quantity Update Successfull!", "success");
                    
                }
            }
            )
    }
    return (
        <Row>
            <Col xs={12} md={12} lg={12} >

                {Users.map((items, index) => {
                  
                   let myamount = parseFloat(items.amount);
                    totalamount+=myamount;
                  console.log("totalamountvat",totalamount)
                    //  totalamount += items.amount;
                     
                 
                    totalqty += items.qty



                    return (<div class="card-header2">
                        <div class="row">
                            <div class="col-12 col-sm-6 col-lg-7 col-md-7">
                                <h6> Total Amount: {totalamount}</h6>
                            </div>
                            <div class="col-12 col-sm-2 col-lg-2 col-md-2">    <h6>Total Qty : {totalqty}</h6>   </div>
                            <div class="col-12 col-sm-2 col-lg-2 col-md-2">    <a type="submit" onClick={senddata} className="btn btn-success Your Order" >Order Comfirm</a> </div>
                            <div class="col-12 col-sm-2 col-lg-1 col-md-1"></div>
                        </div>
                    </div>)

                }

                )

                }
            </Col>
        </Row>

    );
}






export default Footer;