/* eslint-disable import/first */
import React from 'react';
import { BrowserRouter, Route, Switch,withRouter } from 'react-router-dom';
import './App.css';
import login from "./login";
import Registration from "./Registration";
{/* ******************************** Admin    ************************** */ }
import Header from "./Header";
import Eheader from "./Eheader";
import Deshbord from "./Deshbord";
import Dashboard from "./admin/Dashboard";
import Lead from "./admin/Lead";
import Clients from "./admin/Clients";
import Logout from "./admin/Logout";
import adminOrder from "./admin/Order";
import User from "./admin/User";
import Sales from "./admin/Sales";
import PolicyInfo from "./admin/PolicyInfo";
import Risk from "./admin/Risk";
import Risklist from './admin/Risklist';
import Qtyupdate from './admin/Qtyupdate';
import Footer from './Footer';
import Order from './Order';
import Orderlist from './Orderlist';
import Orderstatus from './Orderstatus';
import Payment from './Payment';
import Adminorder from './admin/Order';
import Approvedorder from './admin/Approvedorder';
import Invoicegenreted from './admin/Invoicegenreted';
import Cancelorder from './admin/Cancelorder';
import Paymentlist from './admin/Paymentlist';
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      is_visible: false
    };
  }
  componentDidMount() {
    var scrollComponent = this;
    document.addEventListener("scroll", function (e) {
      scrollComponent.toggleVisibility();
    });
  }
  toggleVisibility() {
    if (window.pageYOffset > 300) {
      this.setState({
        is_visible: true
      });
    } else {
      this.setState({
        is_visible: false
      });
    }
  }
  scrollToTop() {
    window.scrollTo({
      top: 0,
      behavior: "smooth"
    });
  }

  render() {
    const { is_visible } = this.state;
    return (
      <>


        <BrowserRouter >
          <Switch>
            <Route path="/login" component={login} />
            <Route path="/Registration" component={Registration} />
            <Route path="/Orderlist" component={Orderlist} />
            <Route path="/Order" component={Order} />
            <Route path="/Footer" component={Footer} />
            <Route path="/Orderstatus" component={Orderstatus} />
            {/* ******************************** Admin   ************************** */}
            <Route path="/Header" component={Header} />
            <Route path="/Eheader" component={Eheader} />
            <Route path="/Deshbord" component={Deshbord} />
            <Route path="/admin/Dashboard" component={Dashboard} />
            <Route path="/admin/Lead" component={Lead} />
            <Route path="/admin/Clients" component={Clients} />
            <Route path="/admin/Logout" component={Logout} />
            <Route path="/admin/PolicyInfo" component={PolicyInfo} />
            <Route path="/admin/User" component={User} />
            <Route path="/admin/Sales" component={Sales} />
            <Route path="/admin/risk" component={Risk} />
            <Route path="/admin/risklist" component={Risklist} />
            <Route path="/admin/Qtyupdate/:id" component={Qtyupdate} />
            <Route path="/Payment" component={Payment} />
            <Route path="/admin/Order" component={Adminorder} />
            <Route path="/admin/Approvedorder" component={Approvedorder} />
            <Route path="/admin/Invoicegenreted" component={Invoicegenreted} />
            <Route path="/admin/Cancelorder" component={Cancelorder} />
            <Route path="/admin/Paymentlist" component={Paymentlist} />

            {/* ********************************Editor User    ************************** */}

          </Switch>
        </BrowserRouter>
        <div className="scroll-to-top">
          {is_visible && (
            <div onClick={() => this.scrollToTop()}>
              <img src='https://www.freeiconspng.com/uploads/sign-up-button-icon-26.png' alt='Go to top' />
            </div>
          )}
        </div>
      </>
    );
  }
}



export default withRouter(App);