
import { Button } from 'react-bootstrap';
import Table from 'react-bootstrap/Table';
import Eheader from "../Eheader";
import React from 'react';
import ReactToPrint, { PrintContextConsumer } from 'react-to-print';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';

import { useHistory, useParams } from "react-router-dom";
import { useState, useEffect } from 'react'
import swal from 'sweetalert'
const Invoicegenreted = (props) => {

    const [product_name, setproduct_name] = useState('');
    const [product_type, setproduct_type] = useState('');
    const [manufacturer, setmanufacturer] = useState('');
    const [scm1, setscm1] = useState('');
    const [hsncode, sethsncode] = useState('');
    const [pack, setpack] = useState('');
    const [mrp, setmrp] = useState('');
    const [qty, setqty] = useState([]);
    const [rate, setrate] = useState('');
    const [cgst, setcgst] = useState('');
    const [sgst, setsgst] = useState('');
    const [scm2, setscm2] = useState('');
    const [id, setid] = useState('');
    const [Users, SetUser] = useState([""]);

    let history = useHistory();

    var totalamount = 0.00;
    var totalqty = 0.00;

    const saveUser = (items) => {
        console.log("items", items)
        if (items) {
            setproduct_name(items.product_name)
            setproduct_type(items.product_type)
            setmanufacturer(items.manufacturer)
            setscm1(items.scm1)
            setscm2(items.scm2)
            sethsncode(items.hsncode)
            setpack(items.pack)
            setmrp(items.mrp)
            setcgst(items.cgst)
            setqty(items.qty)
            setid(items.id)
            setrate(items.rate)
            setsgst(items.sgst)
        }
    };
    const senddata = () => {
        console.log(product_type, product_name, scm1, hsncode, pack, mrp, rate, cgst, sgst, scm2, qty, manufacturer)
        let date = { product_type, product_name, scm1, hsncode, pack, mrp, rate, cgst, sgst, scm2, qty, manufacturer }
        fetch("http://192.168.1.6:4000/order/", {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-type': 'application/json',
            },
            body: JSON.stringify(date)

        })
            .then(response => response.json())
            .then((resData) => {
                console.log(resData)
                // props.history.push('./Logout');
            }
            )
    }

    useEffect(() => {
        getlist()
    }, []);

    function getlist() {
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
        fetch("http://192.168.1.6:4000/order/", {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Authorization': user_data.jwt,

            },
        })
            .then((result) => {
                result.json()
                    .then((data) => {
                        SetUser(data.list)
                        console.log("datasdwdw", data.list);


                    })
            })
    }
    function deleteUser(id) {
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
        fetch(`http://192.168.1.6:4000/order/${id}`, {
            method: 'DELETE',
            headers: {
                'Accept': 'application/json',
                'Content-type': 'application/json',
                'Authorization': user_data.jwt,

            },
        }).then((result) => {
            result.json().then((resp) => {

                console.log("resp", resp)
                getlist()
            }

            )
        })
    }
    function onSubmit() {
        console.log("hbbhj", id)
        if (id) {
            console.log(product_type, product_name, scm1, hsncode, pack, mrp, rate, cgst, sgst, scm2, qty, manufacturer)
            let date = { product_type, product_name, scm1, hsncode, pack, mrp, rate, cgst, sgst, scm2, qty, manufacturer }

            fetch(`http://192.168.1.6:4000/order/${id}/`, {
                method: 'PUT',
                headers: {
                    'Accept': 'application/json',
                    'Content-type': 'application/json',

                },
                body: JSON.stringify(date)

            })
                .then(response => response.json())
                .then((resData) => {
                    console.log(resData)

                    if (resData.message == "Your order is palced Successfully") {


                        return swal("Thank You!", "Your Order Palced Successfully!", "success");


                    }
                    else
                        return history.push('/admin/Logout');
                }
                )

        }

    };


    return (
        <div>


            <Eheader />
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>

            <div className="fstep">
                <div class="row">
                    <div class="col-lg-6 col-md-6">
                        <h6>Invoice Genreted</h6>
                    </div>
                    <div class="col-lg-6 col-md-6">
                        <ReactHTMLTableToExcel
                            className="refresh"
                            id="refresh"
                            table="emp"
                            filename="ReportExcel"
                            sheet="Sheet"
                            buttonText="Excel" />
                        <ReactToPrint content={() => this.componentRef}>
                            <PrintContextConsumer>
                                {({ handlePrint }) => (
                                    <button className="refresh" onClick={handlePrint}>PDF</button>
                                )}
                            </PrintContextConsumer>
                        </ReactToPrint>
                       
                    </div>
                </div>
                <Table striped bordered hover >
                    <thead>
                        <tr>
                            {/* <th><span>SR/No</span></th> */}
                            <th><span>PRODUCT NAME</span></th>
                            <th><span>QTY</span></th>
                            <th><span>MANUFACTURE</span></th>
                            <th><sapn>HSN CODE</sapn></th>
                            <th><span>SCM 1</span></th>
                            <th><span>SCM 2</span></th>
                            <th><span>PACK</span></th>
                            <th><span>MRP</span></th>
                            <th><span>RATE</span></th>
                            <th><span>AMOUNT</span></th>
                            <th><span>ACTION</span></th>

                        </tr>
                    </thead>
                    {

                        Users && Users.map((items, index) => {
                            totalamount += items.amount;
                            totalqty += items.qty;

                            //  setqty(items.qty);

                            return (
                                <tr>
                                    {/* <td class="ty-09">
                        <p class="tk-10"><span className="details"></span>{totalamount}</p>
                    </td> */}
                                    <td class="ty-09">
                                        <span class="label label-default">{items.product_name}</span>
                                    </td>
                                    <td class="ty-09">
                                        <p class="label label-default">{items.qty}</p>

                                    </td>
                                    <td class="ty-09">
                                        <p class="label label-default">{items.manufacturer}</p>
                                    </td>
                                    <td class="ty-09">
                                        <p class="label label-default">{items.hsncode}</p>
                                    </td>
                                    <td class="ty-09">
                                        <p class="label label-default">{items.scm1}</p>
                                    </td>
                                    <td class="ty-09">
                                        <p class="label label-default">{items.scm2}</p>
                                    </td>
                                    <td class="ty-09">
                                        <p class="label label-default">{items.pack}</p>
                                    </td>
                                    <td class="ty-09">
                                        <p class="label label-default">{items.mrp}</p>
                                    </td>

                                    <td class="ty-09">
                                        <span class="label label-default">{items.rate}</span>
                                    </td>
                                    <td> <span className="details1">{items.amount} </span>
                                    </td>
                                    <td >


                                        <div id="popup1" class="overlay">
                                            <div class="popup">
                                                <h4>Update Quantity</h4>
                                                <a class="close" href="#">&times;</a>
                                                <div class="content">
                                                    <input type="number" className="form-control form-control-lg" name="qty" value={qty} onChange={(e) => setqty(e.target.value)} />
                                                    <Button type="submit" id="savedata" className="btn save-btn  Insurt " onClick={() => onSubmit(items.id)} >Insurt</Button>
                                                </div>
                                            </div>
                                        </div>
                                        <a href="#popup1" onClick={() => saveUser(items)} class="table-link text-info" >

                                            <span class="fa-stack myDIV" id="edit">
                                                <i class="fa fa-square fa-stack-2x"></i>
                                                <i class="fa fa-pencil fa-stack-1x fa-inverse"></i>
                                            </span>

                                        </a>
                                        <a class="table-link " href="#" onClick={() => deleteUser(items.id)}>
                                            <span class="fa-stack">
                                                <i class="fa fa-square fa-stack-2x"></i>
                                                <i class="fa fa-trash-o fa-stack-1x fa-inverse"></i>
                                            </span>
                                        </a>
                                    </td>

                                </tr>
                            )

                        }

                        )

                    }
                </Table>

            </div>

        </div >
    )
}



export default Invoicegenreted;




