
import Table from 'react-bootstrap/Table';
import Header from "../Header";
import { Link } from 'react-router-dom'
import React, { useRef, useState, useEffect } from 'react';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import jwt_decode from "jwt-decode";
import Popup from 'reactjs-popup';
import ReactToPrint, { PrintContextConsumer } from 'react-to-print';
// import Loader from '../Spinner'
import Email from "../email";
function Lead(props) {

    const [status, setstatus] = useState("");
    const [insurance_amount_details, setinsurance_amount_details] = useState("");
    const [remark, setremark] = useState("");
    const [payment_mode, setpayment_mode] = useState("")
    const [referance_code, setreferance_code] = useState("");
    const [transaction_id, settransaction_id] = useState("");
    const [Users, SetUser] = useState([""]);
    const [pay_amount, setpay_amount] = useState([""])
    const componentRef = useRef();
    
    const componentRef2 = useRef();
    const [errorMsg, setErrorMsg] = useState("");
    // const [Spinner, setState] = useState(true);
    

    useEffect(() => {
        getlist()
    }, []);
    // var  Spinner =  true;
    function getlist() {
        
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
       
        fetch("https://connexialfintech.pythonanywhere.com/policy/com/", {
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json; charset=utf8',
                'Authorization': user_data.jwt,
            },
        })
            .then((result) => {
                result.json()
                    .then((data) => {
                        SetUser(data)
                        // setState({Spinner:false })
                        console.log("data", data);

                    })
            })

    }
    function deleteUser(id) {
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
        fetch(`https://connexialfintech.pythonanywhere.com/policy/com/${id}`, {
            method: 'DELETE',
            headers: {
                'Accept': 'application/json',
                'Content-type': 'application/json',
                'Authorization': user_data.jwt,

            },

        }).then((result) => {
            result.json().then((resp) => {

                console.log("resp", resp)
                getlist()
            }

            )
        })

    }

    const senddata = (e) => {
        if (insurance_amount_details.trim() == "") {
            // console.log("Pls Enter Name")
            setErrorMsg('Please Enter Enter Client Id ')
            return false;
        }
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
        console.log(status, payment_mode, transaction_id, remark, referance_code, insurance_amount_details, pay_amount)
        let date = { status, payment_mode, transaction_id, remark, referance_code, insurance_amount_details, pay_amount }
        console.log("gfgfgg", date)
        fetch("https://connexialfintech.pythonanywhere.com/policy/paymentmode/", {
            method: 'POST',

            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json; charset=utf8',
                'Authorization': user_data.jwt,
            },
            redirect: 'follow',
            referrer: 'no-referrer',
            body: JSON.stringify(date)

        })
            .then(response => response.json())
            .then((responsejson) => {

                console.log("rajevv", responsejson);
                props.history.push('/admin/Thankyou');
                // if (responsejson.status == "Approved") {
                //     alert("Your Payment Successful")

                //     return  
                //   }
            })


    }

    const vehicleInfo = JSON.parse(localStorage.getItem('insurance_type'));

    localStorage.removeItem('insurance_type')
    const token = JSON.parse(localStorage.getItem('myData'));
    const decoded = jwt_decode(token.jwt);

    return (

        <div id="msform" >
            <Header />
            <br></br>

            <br></br>
            {/* {Spinner && <Loader/>} */}
            <br></br>
            <br></br>
            <div class="card-body">



                <div class="row">
                    <div class="col-lg-6 col-md-6">
                        <h6> ALL LEADS</h6>
                    </div>
                    <div class="col-lg-6 col-md-6">
                        <ReactToPrint
                            trigger={() => <button className="refresh">PDF</button>}
                            content={() => componentRef.current}
                        />
                        <ReactHTMLTableToExcel
                            className="refresh"
                            table="emp"
                            filename="ReportExcel"
                            sheet="Sheet"
                            buttonText="Excel" />
                    </div>
                </div>






                <div class="row">
                    <Table id="emp" striped bordered hover ref={componentRef}>

                        <thead>
                            <tr >
                                <th><span>Quote</span></th>
                                <th><span>Client / Lead source</span></th>

                                <th ><span>Policy Details</span></th>
                                <th><span>Sum Insured</span></th>

                                <th>Action</th>

                            </tr>
                        </thead>
                        {Users.map((items, index) =>
                            items.client && items.client.insurance_amount_details ? (
                                null
                            ) :
                                <tbody className="lead" >

                                    <tr id="emp" striped bordered hover  >
                                        <td class="">
                                            <span class="label label-default tk-10"><p>{items.client ? items.client.id : 0}</p></span>
                                        </td>
                                        <td>

                                            <a class="user-link">{items.title}. {items.client_name}</a>
                                            <p class="user-subhead"><span className="details">Email: </span><a href="mailto: abc@example.com">{items.email1}</a></p>
                                            <p class="user-subhead"><span className="details">Contact: </span><a href="tel:01234567890">{items.mobile_no1}</a></p>
                                            <hr></hr>
                                            <a href="#" onClick={() => window.open("Salesprofile")} class="user-link">{decoded.full_name} </a>
                                            <span className="details">( {decoded.user_type})</span>

                                        </td>
                                        <td class=" ty-09 ">
                                            <p><span class="label label-default"><span><b>Insurer : </b></span>{items.insurer}</span></p>
                                            {
                                                items.insured_name && items.insured_name ? (
                                                    <p><span class="label label-default"><span><b>Insured Name : </b></span>{items.insured_name}</span></p>
                                                ) : <p><span class="label label-default"><span><b>Insured Name : </b></span>{items.title}. {items.client_name}</span></p>




                                            }

                                            <p><span class="label label-default rt"><span><b>Insurance : </b></span>{items.insurance_type}</span></p>
                                            <p><span class="label label-default"><span><b>Registration No : </b></span>{items.registration_number}</span></p>
                                            <p><span class="label label-default"><span><b>Sum Insured : </b></span>{items.sum_insure}.00</span></p>
                                            <p><span class="label label-default"><span><b>Period of insurance : </b></span>{items.date_from} To {items.Date_to}</span></p>
                                        </td>
                                        <td class=" ty-09">
                                            <p><span class="label label-default"><span><b>Basic Premium : </b></span>{items.client ? items.client.actual_amount : 0}0</span></p>
                                            <p><span class="label label-default"><span><b>VAT Amount : </b></span>{items.client ? items.client.vat_amount : 0}0</span></p>
                                            <p><span class="label label-default"><span><b>Total Amount : </b></span>{items.client ? items.client.premium_with_vat : 0}0 </span></p>
                                            <p><span class="label label-default"><span><b>Total Receivable : </b></span>{items.client ? items.client.premium_with_vat : 0}0 </span></p>

                                        </td>


                                        <td >

                                            <Popup trigger={<Link to={items.client ? items.client.id : 0} class="table-link text-info">

                                                <span class="fa-stack myDIV">
                                                    <i class="fa fa-square fa-stack-2x"></i>
                                                    <i class="fa fa-envelope fa-stack-1x fa-inverse"></i>
                                                </span>
                                                <div class="hide">Send Email </div>
                                            </Link>} position="top left">
                                                {close => (
                                                    <div>
                                                        <a className="close" onClick={close}>

                                                            &times;
                                                        </a>
                                                        <Email />



                                                    </div>
                                                )}
                                            </Popup>

                                            <Link to={"UpdateQuotation/" + items.id} class="table-link text-info">

                                                <span class="fa-stack myDIV">
                                                    <i class="fa fa-square fa-stack-2x"></i>
                                                    <i class="fa fa-pencil fa-stack-1x fa-inverse"></i>
                                                </span>
                                                <div class="hide">Edit Profile </div>
                                            </Link>
                                            <Link to={"AddQuotation/" + items.id} class="table-link text-info">

                                                <span class="fa-stack myDIV">
                                                    <i class="fa fa-square fa-stack-2x"></i>
                                                    <i class="fa fa-user-plus fa-stack-1x fa-inverse"></i>
                                                </span>
                                                <div class="hide">Add New Quotation</div>
                                            </Link>
                                            <a href="#" onClick={() => deleteUser(items.id)} class="table-link danger">
                                                <span class="fa-stack myDIV">
                                                    <i class="fa fa-square fa-stack-2x"></i>
                                                    <i class="fa fa-trash-o fa-stack-1x fa-inverse"></i>
                                                </span>
                                                <div class="hide">Delete Lead</div>
                                            </a>

                                            <Popup trigger={<Link to={+ items.id} class="table-link text-info">

                                                <span class="fa-stack myDIV">
                                                    <i class="fa fa-square fa-stack-2x"></i>
                                                    <i class="fa fa-credit-card fa-stack-1x fa-inverse"></i>
                                                </span>
                                                <div class="hide">Send Payment</div>
                                            </Link>} position="top left">
                                                {close => (
                                                    <div>
                                                        <a className="close" onClick={close}>

                                                            &times;
                                                        </a>
                                                        <form id="msform" >


                                                            <Row>
                                                                <Col xs={12} md={12} lg={8} >
                                                                    <h6>Payment </h6>
                                                                </Col>
                                                                <Col xs={12} md={12} lg={4} >
                                                                    <h6>Client Id : {items.client ? items.client.id : 0} </h6>
                                                                </Col>
                                                            </Row>
                                                            <p className="error">{errorMsg}  </p>
                                                            <hr></hr>
                                                            <Row>
                                                                <Col xs={12} md={12} lg={12} >
                                                                    <label for="size_1">Status <em>*</em></label>
                                                                    <select name="status" value={status} onChange={(e) => { setstatus(e.target.value) }}>
                                                                        <option >Select</option>
                                                                        <option value="Approved"> Approved</option>
                                                                        <option value="Pending">Pending</option>
                                                                        <option value="Cancelled">Cancelled</option>

                                                                    </select>

                                                                </Col>

                                                                {status == "Approved" ? (
                                                                    <Col xs={12} md={12} lg={12} >
                                                                        <label for="size_1">Payment Mode<em>*</em></label>
                                                                        <select name="payment_mode" value={payment_mode} onChange={(e) => { setpayment_mode(e.target.value) }}>
                                                                            <option >Select</option>
                                                                            <option value="Net Banking">Net Banking</option>
                                                                            <option value="Direct Bank">Direct Bank</option>
                                                                            <option value="TT">TT</option>
                                                                            <option value="Cheque">Cheque</option>
                                                                            <option value="Cash">Cash</option>
                                                                        </select>
                                                                    </Col>

                                                                ) : null
                                                                }

                                                                {status == "Approved" ? (
                                                                    <Col xs={12} md={12} lg={12} >
                                                                        <label for="size_1">Payment Amount<em>*</em></label>
                                                                        <input type="text" value={pay_amount} onChange={(e) => { setpay_amount(e.target.value) }} name="pay_amount" />
                                                                    </Col>

                                                                ) : null
                                                                }
                                                                {payment_mode == "Cheque" ? (
                                                                    <Col xs={12} md={12} lg={12} >
                                                                        <label for="size_1">Cheque No <em>*</em></label>
                                                                        <textarea type="text" value={referance_code} onChange={(e) => { setreferance_code(e.target.value) }} name="referance_code" />
                                                                    </Col>
                                                                ) : null
                                                                }
                                                                {payment_mode == "TT" ? (
                                                                    <Col xs={12} md={12} lg={12} >
                                                                        <label for="size_1">Referance No <em>*</em></label>
                                                                        <textarea type="text" value={referance_code} onChange={(e) => { setreferance_code(e.target.value) }} name="referance_code" />
                                                                    </Col>
                                                                ) : null
                                                                }
                                                                {payment_mode == "Net Banking" ? (
                                                                    <Col xs={12} md={12} lg={12} >
                                                                        <label for="size_1">Referance Code <em>*</em></label>
                                                                        <textarea type="text" value={referance_code} onChange={(e) => { setreferance_code(e.target.value) }} name="referance_code" />
                                                                    </Col>
                                                                ) : null
                                                                }
                                                                {payment_mode == "Direct Bank" ? (
                                                                    <Col xs={12} md={12} lg={12} >
                                                                        <label for="size_1">Transaction Id<em>*</em></label>
                                                                        <textarea type="text" value={transaction_id} onChange={(e) => { settransaction_id(e.target.value) }} name="transaction_id" />
                                                                    </Col>
                                                                ) : null
                                                                }

                                                                {status == "Pending" ? (
                                                                    <Col xs={12} md={12} lg={12} >
                                                                        <label for="size_1">Remark <em>*</em></label>
                                                                        <textarea type="text" value={remark} onChange={(e) => { setremark(e.target.value) }} name="remark" />
                                                                    </Col>
                                                                ) : null
                                                                }
                                                                {status == "Cancelled" ? (
                                                                    <Col xs={12} md={12} lg={12} >
                                                                        <label for="size_1">Remark <em>*</em></label>
                                                                        <textarea type="text" value={remark} onChange={(e) => { setremark(e.target.value) }} name="remark" />
                                                                    </Col>
                                                                ) : null
                                                                }
                                                                {status == "Required" ? (
                                                                    <Col xs={12} md={12} lg={12} >
                                                                        <label for="size_1">Remark <em>*</em></label>
                                                                        <textarea type="text" value={remark} onChange={(e) => { setremark(e.target.value) }} name="remark" />
                                                                    </Col>
                                                                ) : null
                                                                }

                                                                <Col xs={12} md={12} lg={12} >

                                                                    <label for="size_1">Enter Client Id <em>*</em></label>
                                                                    <input type="text" value={insurance_amount_details} onChange={(e) => { setinsurance_amount_details(e.target.value) }} name="insurance_amount_details" />

                                                                </Col>


                                                                <Col xs={12} md={9} lg={9} >  </Col>
                                                                <Col xs={12} md={3} lg={3} >
                                                                    <div className="butan">
                                                                        <Link to={"./lead"} type="submit" className="btn btn-success" onClick={senddata} >Save </Link>


                                                                    </div>
                                                                </Col>

                                                            </Row>

                                                            <br></br>





                                                        </form>



                                                    </div>
                                                )}
                                            </Popup>

                                            <br></br>




                                            <Popup trigger={<Link to={+ items.id} className="table-link text-info Download Quotation">

                                            <span className="RiskDOWN2"> Download Quotation </span>
                                            </Link>} position="top left">
                                                {close => (
                                                    <div>
                                                        <a className="close" onClick={close}>

                                                            &times;
                                                        </a>
                                                        <div className="row" >


                                                            <a href="#" >
                                                                <ReactToPrint
                                                                    trigger={() => <sapn className="refresh">Print Quotation</sapn>}
                                                                    content={() => componentRef2.current}
                                                                >


                                                                </ReactToPrint>
                                                            </a>
                                                            <table ref={componentRef2}>
                                                                <div class="row name-09">
                                                                    <div class="tt">

                                                                        <img className="qute_img" src="/fintech.png" alt="img"></img>
                                                                        <br></br>
                                                                    </div>
                                                                </div>
                                                                <div class="row bg-dark">
                                                                    <h5 className="QUOTATION">QUOTATION
                                                                    </h5>
                                                                </div>
                                                                <div class="row name-09">
                                                                    <div className="t">
                                                                        <h6>Quote No.

                                                                            : {items.id} </h6>
                                                                        <h6 className="Date">Date :

                                                                            {items.Date_to}</h6>
                                                                    </div></div>
                                                                <div class="row name-09">
                                                                    <table>
                                                                        <tr>
                                                                            <td>Client Name</td>
                                                                            <td colspan="2">{items.title}. {items.client_name}</td>
                                                                            <td>Cover Period </td>
                                                                            <td colspan="2">{items.date_from} - to - {items.Date_to}
                                                                            </td>
                                                                        </tr>
                                                                        <tr>
                                                                            <td>Address</td>
                                                                            <td colspan="4">{items.address}
                                                                            </td>

                                                                        </tr>
                                                                        <tr>
                                                                            <td>Insurer Name</td>
                                                                            <td colspan="2">{items.insurer}
                                                                            </td>
                                                                            <td>Intermediary Name  </td>
                                                                            <td colspan="2">{items.insurer}

                                                                            </td>
                                                                        </tr>
                                                                        <tr>
                                                                            <td>Insured Name/ Insurance Class <br />
                                                                                Vehicle Registration/ Make <br /> / Model/ Color /
                                                                                Year of <br />
                                                                                Manufacture
                                                                            </td>
                                                                            <td>Sum Insured <br />

                                                                            </td>
                                                                            <td>Besic Premium </td>

                                                                            <td>VAT </td>
                                                                            <td></td>
                                                                            <td>Total (Inc.vat)</td>

                                                                        </tr>
                                                                        <tr>
                                                                            <td>
                                                                                {items.insurance_type}<br /> {items.insurance_class} <br />
                                                                                {items.registration_year}<br></br>{items.vehicle_make}  <br />
                                                                                {items.model_number}{items.color} <br /> {items.manufacture_year} <br />


                                                                            </td>
                                                                            <td>{items.sum_insure}.00 </td>
                                                                            <td>{items.client ? items.client.premium_excluding_vat : 0}.00 </td>

                                                                            <td>
                                                                                {items.client ? items.client.vat_amount : 0}.00 </td>
                                                                            <td></td>
                                                                            <td>
                                                                                {items.client ? items.client.premium_with_vat : 0}.00 </td>


                                                                        </tr>

                                                                        <tr>
                                                                            <th colspan="2">
                                                                                TOTAL AMOUNT

                                                                            </th>
                                                                            <th> </th>
                                                                            <th></th>
                                                                            <th></th>
                                                                            <td>  {items.client ? items.client.premium_with_vat : 0}.00</td>
                                                                        </tr>
                                                                        <tr>
                                                                            <th colspan="5">
                                                                                ADMINISTRATION CHARGES

                                                                            </th>
                                                                            <th>0.00 </th>

                                                                        </tr>
                                                                        <tr>
                                                                            <th colspan="5">
                                                                                Total Amount

                                                                            </th>
                                                                            <th>{items.client ? items.client.premium_with_vat : 0}.00 </th>

                                                                        </tr>
                                                                        <tr>
                                                                            <th colspan="6">TIN: 100-688-255</th>
                                                                        </tr>
                                                                    </table>
                                                                </div>




                                                                <div class="row ">




                                                                    <div class="col-md-5">
                                                                        FOLLOWING ARE OUR BANK DETAILS: <br />
                                                                        Account Name: {items.insurer}  <br />
                                                                        Bank Name: <br />
                                                                        Branch Name:  <br />
                                                                        Notes:  <br />
                                                                        The payment should be made in favor of the .....insurer name  <br />

                                                                    </div>
                                                                    <div class="col-md-4">

                                                                    </div>
                                                                </div>

                                                                <div class="row name-09">
                                                                    <h3 className="name-09">ISSUED BY, {items.insurer}</h3>

                                                                </div>

                                                                <div class="row name-09">
                                                                    <h3>
                                                                        ____________________________________________________

                                                                        <br /><p className="name-093">
                                                                            For, {items.insurer}</p>
                                                                    </h3>

                                                                </div>
                                                                <div class="row">
                                                                    <div className="name-09">
                                                                        <h6>Customer Declaration: <br />
                                                                            1. I/We declare that the above quote is given to me/us on the information provided by me/us. <br />
                                                                            2. I/We declare to the best of my/our knowledge and belief that the information given on this quote is true in every respect. <br />
                                                                            3. I/We agree that this proposal and declaration shall be the basis of the contract between me/us and the Insurer. <br />
                                                                            4. I/We confirm to have been given adequate pre-sale and post-sale advice relating to coverage, terms and conditions of this <br />
                                                                            insurance product.
                                                                        </h6>
                                                                    </div>


                                                                </div>
                                                                <div class="row">
                                                                    <div class="sign">
                                                                        <h1>  ___________________________
                                                                            <br />
                                                                            Signature </h1>
                                                                        <h1>___________________________ <br />
                                                                            Date
                                                                        </h1>
                                                                    </div>
                                                                </div>
                                                                <hr></hr>
                                                                <div class="row name-09">
                                                                    <p>
                                                                        IMPORTANT NOTICE: Failure to disclose material facts could result in your contract being invalidated/cancelled, a claim not
                                                                        being paid or difficulty in obtaining insurance in the future. If you are in doubt as to whether a fact is material you should
                                                                        disclose it. The Insurer reserves the right to decline any proposal

                                                                    </p>

                                                                </div>

                                                            </table>

                                                        </div>
                                                    </div>

                                                )}
                                            </Popup>

                                        </td>
                                    </tr>



                                </tbody>
                        )
                        }


                    </Table>


                </div>
            </div>
        </div>

    );
}


export default Lead;