import React, { Component } from 'react';
import Eheader from '../Eheader';
import Pie from './Pie';
import Bar from './Bar';
import AreaChart from './AreaChart';
import jwt_decode from "jwt-decode";
class Dashboard extends Component {
    constructor() {
        super()
        this.state = {
            SearchData: {},
            items: [],

        }
    }

    componentDidMount() {

        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)

        this.setState({ SearchData: user_data }, () => {
            console.log("dyugfgsdfgy", this.state.SearchData)
        })
        const token = JSON.parse(localStorage.getItem('myData'));
        const decoded = jwt_decode(token.jwt);
        const usertokan = decoded.id
        console.log("decoded", decoded);
        fetch('https://connexialfintech.pythonanywhere.com/policy/DadhbordCountViewSet/', {

            method: 'GET',
            headers: {

                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Authorization': user_data.jwt,
            },
        })


            .then((res) => res.json())
            .then((data) => {
                this.setState({
                    items: data,
                })
                console.log("vvvveeev", this.state.items)
            })
            .catch((error) => {
                console.log("MYerror", error)

            })


    }
    render() {
        return (

            <div>
                <Eheader senddat={this.state.SearchData} />
                <br></br>

                <br></br>
                <br></br>
                <br></br>

                <div class="card">
                    <div class="card-header">
                        <div class="row">
                            <div class="col-lg-6 col-md-6">
                                Operational Dashboard <span>Daily Statistics</span>
                            </div>
                        </div>
                    </div>
                    <div class="card-body">
                        {this.state.items.map((items) =>
                            <div class="row">
                                <div class="col-md-3">
                                    <div class="stati turquoise " >
                                        
                                        <i class="fa fa-user-circle-o"></i>
                                        <div>
                                            <b>{items.user}</b>
                                            <a href="#" onClick={() => window.open("./User", "_self")}> <span>Users</span> </a>
                                        </div>
                                       
                                    </div>
                                </div>
                                <div class="col-md-3">
                                    <div class="stati bg-turquoise">
                                        <i class="fa fa-users"></i>
                                        <div>
                                            <b>{items.client}</b>
                                            <a href="#" onClick={() => window.open("./Clients", "_self")}> <span>Clients</span></a>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-md-3">
                                    <div class="stati bg-turquoise ">
                                        <i class="fa fa-hourglass-half"></i>
                                        <div>
                                            <b>{items.leads}</b>
                                            <a href="#" onClick={() => window.open("./Lead", "_self")}> <span>Leads</span></a>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-md-3">
                                    <div class="stati turquoise">
                                        <i class="fa fa-bar-chart"></i>
                                        <div>
                                            <b>{items.sales}</b>
                                            <a href="#" onClick={() => window.open("./Sales", "_self")}> <span>Sales</span></a>
                                        </div>
                                    </div>
                                </div>


                            </div>
                        )}
                    </div>

               

                    <br></br>
                    <br></br>

                <div class="card-header">
                    <div class="row">
                        <div class="col-lg-6 col-md-6">
                            Business Chart By Insurance Class
                        </div>
                        <div class="col-lg-6 col-md-6">
                            <button className="refresh" onClick={() => window.location.reload(false)}>Refresh</button>
                        </div>
                    </div>
                </div>
                <div class="card-body">
                    <div class="row">

                        <div class="col-lg-4 col-md-6 item">
                            <div class=" item-card card-block">
                                <div class=" panel-primary">
                                    <h6>Insurance Types</h6>
                                    <hr></hr>
                                    <Pie />
                                </div>
                            </div>
                        </div>
                        <div class="col-lg-4 col-md-6 item">
                            <div class=" item-card card-block">
                                <div class=" panel-primary">
                                    <h6>Leads Performance</h6>
                                    <hr></hr>
                                    <Bar />
                                </div>
                            </div>
                        </div>
                        <div class="col-lg-4 col-md-6 item">
                            <div class=" item-card card-block">
                                <div class=" panel-primary">
                                    <h6>Target Vs Sales Performance</h6>
                                    <hr></hr>
                                    <AreaChart />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <br></br>
                <br></br>
                <div class="card-header">
                    <div class="row">
                        <div class="col-lg-6 col-md-6">
                            Insurer Performance
                        </div>
                        <div class="col-lg-6 col-md-6">
                            <button className="refresh" onClick={() => window.location.reload(false)}>Refresh</button>
                        </div>
                    </div>
                </div>
            

        
                </div>


            </div>
        );
    }
}
export default Dashboard;