import React, { Component } from 'react';
import Header from './Header';
import Pie from './admin/Pie';
import Bar from './admin/Bar';
import AreaChart from './admin/AreaChart';
import Table from 'react-bootstrap/Table'
import { Chart } from "react-google-charts";

class Deshbord extends Component {
    constructor() {
        super()
        this.state = {
            list: [],

        }

    }

    componentDidMount() {

        fetch("http://192.168.1.3:4000/business_chart/", {
            method: "GET",

        })
            .then((result) => {
                result.json().then((resp) => {
                    this.setState({ list: resp.list })
                    console.log("vvvvv", this.state.list)
                })
            })

    }

    render() {
        return (

            <div>
                <Header />
                <br></br>

                <br></br>
                <br></br>
                <br></br>
                {/* {this.state.list.map((list, index) => */}
                <div class="card">
                    <div class="card-header">
                        <div class="row">
                            <div class="col-lg-6 col-md-6">
                                Operational Dashboard <span>Daily Statistics</span>
                            </div>
                        </div>
                    </div>
                    <div class="card-body">
                        <div class="row">
                            <div class="col-md-3">
                                <div class="stati turquoise ">
                                    <i class="fa fa-arrow-circle-right"></i>
                                    <div>
                                        <b>12</b>
                                        <span>Purchase Order</span>
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-3">
                                <div class="stati turquoise">
                                    <i class="fa fa-arrow-circle-right"></i>
                                    <div>
                                        <b>10</b>
                                        <span>Order Delivery</span>
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-3">
                                <div class="stati bg-turquoise ">
                                    <i class="fa fa-arrow-circle-right"></i>
                                    <div>
                                        <b>200</b>
                                        <span>Order Cancel</span>
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-3">
                                <div class="stati bg-turquoise">
                                    <i class="fa fa-arrow-circle-right"></i>
                                    <div>
                                        <b>22</b>
                                        <span>Clients</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            
              
                <br></br>
                <div class="card">
                    <div class="row">
                    <div class="col-lg-6 col-md-6">
                            <div class="card">
                            <h6>Insurer Performance</h6>
                                <hr></hr>
                                <Chart
                                  
                                    height={'300px'}
                                    chartType="ComboChart"
                                    loader={<div>Loading Chart</div>}
                                    data={[
                                        [
                                            'Month',
                                            'AAR Insurance Tanzania (Medical)',
                                            'Alliance Insurance Corporation Ltd',
                                            'Alliance Life Assurance Limited',
                                            'Assemble Insurance Tanzania',
                                            'Britam Insurance (Tanzania) Limited',
                                            'Bumaco Insurance Company Limited',
                                            'First Assurance Company Limited',
                                            'GA Insurance Tanzania Limited',
                                            'ICEA LION General Insurance Company (T) Ltd',
                                            'Insurance Group of Tanzania (IGT)',
                                            'Jubilee Life Insurance Corporation of Tanzania Ltd',
                                            'Maxinsure (Tanzania) Limited',
                                            'Mayfair Insurance Company Tanzania Limited',
                                            'Meticulous General Insurance Company Limited',
                                            'Metropolitan Tanzania Life Assurance Co.Ltd',
                                            'MGen Tanzania Insurance Co. Ltd',
                                            'Milembe Insurance Company Limited',
                                            'MO Assurance Company Limited',
                                            'Average',
                                        ],
                                        ['2019/05', 165, 938, 522, 998, 450, 614.6, 165, 938, 522, 998, 450, 614.6, 614.6, 165, 938, 522, 998, 450, 614.6],
                                       
                                    ]}
                                    options={{
                                     
                                        vAxis: { title: 'Income' },
                                        hAxis: { title: 'Last Month' },
                                        seriesType: 'bars',

                                    }}
                                    rootProps={{ 'data-testid': '1' }}
                                />
                            </div>
                        </div>

                        <div class="col-lg-6 col-md-6">
                            <div class="card">
                                <h6>Services In World</h6>
                                <hr></hr>
                                <Chart
                            
                                    height={'300px'}
                                    chartType="GeoChart"
                                    data={[
                                        ['Country', 'Popularity'],
                                        ['tanzania', 700],
                                        ['Usa', 650],
                                        ['india', 650],
                                    
                                    ]}

                                    rootProps={{ 'data-testid': '1' }}
                                />
                            </div>
                        </div>
                       
                    </div>
                </div>

               


                <br></br>

            </div>
        );
    }
}
export default Deshbord