
import { Button } from 'react-bootstrap';
import Table from 'react-bootstrap/Table';
import Eheader from "../Eheader";
import React from 'react';
import ReactToPrint, { PrintContextConsumer } from 'react-to-print';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { useHistory, useParams } from "react-router-dom";
import { useState, useEffect, useRef } from 'react'
import swal from 'sweetalert'
const Order = (props) => {

    const [itemspk, setitemspk] = useState([]);

    const [Users, SetUser] = useState([""]);
    const [status, setstatus] = useState('');

    const [id, setid] = useState('');
    const [product_name, setproduct_name] = useState('');
    const [product_type, setproduct_type] = useState('');
    const [manufacturer, setmanufacturer] = useState('');
    const [scm1, setscm1] = useState('');
    const [hsncode, sethsncode] = useState('');
    const [pack, setpack] = useState('');
    const [mrp, setmrp] = useState('');
    const [qty, setqty] = useState([]);
    const [rate, setrate] = useState('');
    const [cgst, setcgst] = useState('');
    const [sgst, setsgst] = useState('');
    const [scm2, setscm2] = useState('')

    let history = useHistory();
    const componentRef1 = useRef();
    const componentRef2 = useRef();
    var totalamount = 0;
    var totalqty = 0;

    const saveUser = (items) => {
        console.log("items", items)
        if (items) {
            setitemspk(items.confirm_order)
          
        }
    };
    const updateUser = (items) => {
        console.log("items", items)
        if (items) {
          setproduct_name(items.product_name)
          setproduct_type(items.product_type)
          setmanufacturer(items.manufacturer)
          setscm1(items.scm1)
          setscm2(items.scm2)
          sethsncode(items.hsncode)
          setpack(items.pack)
          setmrp(items.mrp)
          setcgst(items.cgst)
          setqty(items.qty)
          setid(items.id)
          setrate(items.rate)
          setsgst(items.sgst)
        }
      };
    useEffect(() => {
        getlist()
    }, []);

    function getlist() {
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
        fetch("http://192.168.1.9:4000/confirm/", {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Authorization': user_data.jwt,

            },
        })
            .then((result) => {
                result.json()
                    .then((data) => {

                        SetUser(data.list)

                        console.log("datasdwdw", data);


                    })
            })
    }
    function deleteUser(id) {
        var Userdetials = localStorage.getItem('myData')
        var user_data = JSON.parse(Userdetials)
        fetch(`http://192.168.1.9:4000/order/${id}`, {
            method: 'DELETE',
            headers: {
                'Accept': 'application/json',
                'Content-type': 'application/json',
                'Authorization': user_data.jwt,

            },
        }).then((result) => {
            result.json().then((resp) => {

                console.log("resp", resp)
                getlist()
            }

            )
        })
    }
    function onSubmit() {
        
        if (id) {
            console.log(product_type, product_name, scm1, hsncode, pack, mrp, rate, cgst, sgst, scm2, status, qty, manufacturer)
            let date = { product_type, product_name, scm1, hsncode, pack, mrp, rate, cgst, sgst, scm2, qty, status, manufacturer }

            fetch(`http://192.168.1.9:4000/AdminAllListView/${id}/`, {
                method: 'PUT',
                headers: {
                    'Accept': 'application/json',
                    'Content-type': 'application/json',

                },
                body: JSON.stringify(date)

            })
                .then(response => response.json())
                .then((resData) => {
                    console.log(resData)

                    if (resData.message == "Your order is palced Successfully") {


                        return swal("Thank You!", "Your Order Palced Successfully!", "success");


                    }
                    else
                        return null
                }
                )

        }

    };

    return (
        <div>


            <Eheader />
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>

            <div className="fstep">
                <div class="row">
                    <div class="col-lg-6 col-md-6">
                        <h6>All Order</h6>
                    </div>
                    <div class="col-lg-6 col-md-6">
                        <ReactHTMLTableToExcel
                            className="refresh"
                            id="refresh"
                            table="emp2"
                            filename="ReportExcel"
                            sheet="Sheet"
                            buttonText="Excel" />
                        <ReactToPrint
                            trigger={() => <button className="refresh">PDF</button>}
                            content={() => componentRef1.current}
                        />

                    </div>
                </div>
                <Table striped bordered hover id="emp2" ref={componentRef1}>
                    <thead>
                        <tr>

                            <th><span>CLIENT NAME</span></th>
                            <th><span>QTY</span></th>
                            <th><span>ORDER NUMBER</span></th>
                            <th><sapn>AMOUNT</sapn></th>
                            <th><span>ACTION</span></th>

                        </tr>
                    </thead>
                    {

                        Users && Users.map((items, index) => {


                            //  setqty(items.qty);

                            return (
                                <tr>


                                    <td class="ty-09">


                                        <p class="label label-default">{items.fullname}</p>

                                    </td>


                                    <td class="ty-09">
                                        <p class="label label-default">{items.total_quantity}</p>
                                    </td>
                                    <td class="ty-09">
                                        <span class="label label-default">{items.id}</span>
                                    </td>
                                    <td> <span className="details1">{items.total_amount} </span>
                                    </td>

                                    <td >
                                        <a href="#popup1" onClick={() => saveUser(items)} class="table-link text-info" >View</a>
                                        <a class="table-link " href="#" onClick={() => deleteUser(items.id)}> Delate</a>
                                    </td>

                                </tr>
                            )

                        }

                        )

                    }
                </Table>
                <div id="popup1" class="overlay">
                    <div class="popup2">
                        <div class="row">
                            <div class="col-lg-6 col-md-6">
                                <h5 className="Details">Order Details</h5>
                            </div>
                            <div class="col-lg-6 col-md-6">
                                <ReactHTMLTableToExcel
                                    className="refresh"
                                    id="refresh"
                                    table="emp"
                                    filename="ReportExcel"
                                    sheet="Sheet"
                                    buttonText="Excel" />
                                <ReactToPrint
                                    trigger={() => <button className="refresh">PDF</button>}
                                    content={() => componentRef2.current}
                                />



                                <a class="close" href="#">&times;</a>
                            </div>
                        </div>




                        <div class="content">
                            <Table striped bordered hover >
                                <thead>
                                    <tr>
                                        {/* <th><span>SR/No</span></th> */}
                                        <th><span>PRODUCT NAME</span></th>
                                        <th><span>QTY</span></th>
                                        <th><span>MANUFACTURE</span></th>
                                        <th><sapn>HSN CODE</sapn></th>
                                        <th><span>SCM 1</span></th>
                                        <th><span>SCM 2</span></th>
                                        <th><span>PACK</span></th>
                                        <th><span>MRP</span></th>
                                        <th><span>RATE</span></th>
                                        <th><span>AMOUNT</span></th>
                                        <th><span>ACTION</span></th>

                                    </tr>
                                </thead>
                                {

                                    itemspk.map((items, index) => {
                                        let myamount = parseFloat(items.amount);
                                        totalamount += myamount;
                                        console.log("totalamountvat", totalamount)
                                        //  totalamount += items.amount;


                                        totalqty += items.qty

                                        return (
                                            <tr>

                                                <td class="ty-09">
                                                    <span class="label label-default" key={items.id}>{items.product_name}</span>
                                                </td>
                                                <td class="ty-09">
                                                    <p class="label label-default">{items.qty}</p>

                                                </td>
                                                <td class="ty-09">
                                                    <p class="label label-default">{items.manufacturer}</p>
                                                </td>
                                                <td class="ty-09">
                                                    <p class="label label-default">{items.hsncode}</p>
                                                </td>
                                                <td class="ty-09">
                                                    <p class="label label-default">{items.scm1}</p>
                                                </td>
                                                <td class="ty-09">
                                                    <p class="label label-default">{items.scm2}</p>
                                                </td>
                                                <td class="ty-09">
                                                    <p class="label label-default">{items.pack}</p>
                                                </td>
                                                <td class="ty-09">
                                                    <p class="label label-default">{items.mrp}</p>
                                                </td>

                                                <td class="ty-09">
                                                    <span class="label label-default">{items.rate}</span>
                                                </td>
                                                <td> <span className="details1">{items.amount} </span>
                                                </td>
                                                <td >


                                                    <a href="#popup12" class="table-link text-info" onClick={() => updateUser(items)} >Status</a>

                                                </td>


                                            </tr>

                                        )

                                    }

                                    )

                                }

                            </Table>
                            <br />  <br />


                            <div class="card-header4">

                                <div class="row">
                                    <div class="col-12 col-sm-6 col-lg-6 col-md-6">
                                        <h6> Total Amount: {totalamount}</h6>
                                    </div>
                                    <div class="col-12 col-sm-2 col-lg-2 col-md-2">    <h6>Total Qty : {totalqty}</h6>   </div>
                                    <div class="col-12 col-sm-2 col-lg-2 col-md-2">
                                    </div>
                                    <div class="col-12 col-sm-2 col-lg-2 col-md-2">   </div>

                                </div>

                            </div>
                        </div>
                    </div>
                </div>
                <div id="popup12" class="overlay">
                    <div class="popup">

                        <h6>Update Quantity</h6>
                        <a class="close" href="#">&times;</a>
                        <hr />
                        <div class="content">
                            <select value={status} onChange={(e) => { setstatus(e.target.value) }} className="form-control" name="insurance_sub">
                                <option >Please Select</option>
                                <option value="Confirm">Confirm</option>
                                <option value="Cancel">Cancel</option>

                            </select>
                            <Button type="submit" id="savedata" className="btn save-btn  Insurt " onClick={onSubmit} >save</Button>
                        </div>
                    </div>

                </div>
            </div>

        </div >
    )
}



export default Order;




