
import Header from "../Header";
import React from 'react';
import { Link } from 'react-router-dom'
import Row from 'react-bootstrap/Row';
import ReactToPrint, { PrintContextConsumer } from 'react-to-print';

import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import Col from 'react-bootstrap/Col';
import Table from 'react-bootstrap/Table';

class Clients extends React.Component {

  constructor() {
    super()
    this.state = {
      SearchData: null,


    }
  }


  SearchInsured(key) {
    var Userdetials = localStorage.getItem('myData')
    var user_data = JSON.parse(Userdetials)
    console.warn(key)
    fetch(`https://connexialfintech.pythonanywhere.com/adminclientlistfilter/?search=` + key, {
      method: 'GET',
      headers: {

        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Authorization': user_data.jwt,
      },
    }).then((data) => {
      data.json().then((resp) => {
        console.warn("resp", resp)
        this.setState({ SearchData: resp })

      })
    })

  }

  Searchid(key) {
    var Userdetials = localStorage.getItem('myData')
    var user_data = JSON.parse(Userdetials)
    console.warn(key)
    fetch(`https://connexialfintech.pythonanywhere.com/adminclientlistfilter/?id=` + key, {
      method: 'GET',
      headers: {

        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Authorization': user_data.jwt,
      },
    }).then((data) => {
      data.json().then((resp) => {
        console.warn("resp", resp)
        this.setState({ SearchData: resp })

      })
    })

  }

  SearchLocation(key) {
    var Userdetials = localStorage.getItem('myData')
    var user_data = JSON.parse(Userdetials)
    console.warn(key)
    fetch(`https://connexialfintech.pythonanywhere.com/adminclientlistfilter/?address=` + key, {
      method: 'GET',
      headers: {

        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Authorization': user_data.jwt,
      },
    }).then((data) => {
      data.json().then((resp) => {
        console.warn("resp", resp)
        this.setState({ SearchData: resp })

      })
    })

  }
  SearchDob(key) {
    var Userdetials = localStorage.getItem('myData')
    var user_data = JSON.parse(Userdetials)
    console.warn(key)
    fetch(`https://connexialfintech.pythonanywhere.com/adminclientlistfilter/?date_of_birth=` + key, {
      method: 'GET',
      headers: {

        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Authorization': user_data.jwt,
      },
    }).then((data) => {
      data.json().then((resp) => {
        console.warn("resp", resp)
        this.setState({ SearchData: resp })

      })
    })

  }
  deleteUser(id) {
    var Userdetials = localStorage.getItem('myData')
    var user_data = JSON.parse(Userdetials)
    fetch(`https://connexialfintech.pythonanywhere.com/policy/com/${id}`, {
      method: 'DELETE',
      headers: {
        'Accept': 'application/json',
        'Content-type': 'application/json',
        'Authorization': user_data.jwt,
      },

    }).then((result) => {
      result.json().then((resp) => {
        console.warn(resp)
        this.setState({ SearchData: "" })

      })
    })
  }

  render() {

    return (
      <div>
        <Header />
        <br></br>
        <br></br>
        <br></br>
        <br></br>
        <div id="msform">

          <div className="fstep">
            <fieldset>
              <div class="row">
                <div class="col-lg-6 col-md-6">
                  <h6>Client List</h6>
                </div>
                <div class="col-lg-6 col-md-6">
                <ReactHTMLTableToExcel
                    className="refresh"
                    id="refresh"
                    table="emp"
                    filename="ReportExcel"
                    sheet="Sheet"
                    buttonText="Excel" />
                  <ReactToPrint content={() => this.componentRef}>
                    <PrintContextConsumer>
                      {({ handlePrint }) => (
                        <button className="refresh" onClick={handlePrint}>PDF</button>
                      )}
                    </PrintContextConsumer>
                  </ReactToPrint>
                  <button className="refresh" onClick={() => window.open("./Add_User", "_self")}>Add</button>

                  

                </div>
              </div>

              <hr></hr>
              <form>

                <Row>
                <Col xs={12} md={2} lg={2} >
                    <label for="size_1">Client Name</label>
                    <input type="text" onChange={(event) => this.SearchInsured(event.target.value)} name="Insured_Name" />
                  </Col>


                  <Col xs={12} md={2} lg={2} >
                    <label for="size_1">Profile id</label>
                    <input type="text" onChange={(event) => this.Searchid(event.target.value)} name="id" />
                  </Col>
                  <Col xs={12} md={2} lg={2} >
                    <label for="size_1">Location</label>
                    <input type="text" onChange={(event) => this.SearchLocation(event.target.value)} name="Debit" />
                  </Col>

                  <Col xs={12} md={2} lg={2} >
                    <label for="size_1">Date of Birth</label>
                    <input type="date" onChange={(event) => this.SearchDob(event.target.value)} name="Vehicle" />
                  </Col>

                </Row>
              </form>
            </fieldset>



          </div>

          <div className="fstep">

            {
              this.state.SearchData ?
              <Table striped bordered hover id="emp" ComponentToPrint ref={el => (this.componentRef = el)}>


                  <thead>
                    <tr>

                      <th><span>PROFILE ID</span></th>
                      <th><sapn>CLIENT NAME</sapn></th>
                      <th><span>PERSONAL INFORMATION </span></th>
                      <th><span>CONTACT INFORMATION</span></th>
                      <th><span>DATE OF BIRTH</span></th>
                      <th><span>LOCATION</span></th>

                      <th><span>ACTION</span></th>

                    </tr>
                  </thead>



                  {
                    this.state.SearchData.map((items) =>


                      <tr>
                        <td class="ty-09">
                          <p class="tk-10"><span className="details">{items.id}</span></p>
                        </td>

                        <td class="ty-09">
                          <span class="label label-default">{items.client_name}</span>
                        </td>
                        <td class="ty-09">
                          <p class="label label-default">Client Status : {items.client_status} | Nationality : {items.nationality}</p>

                          <p class="label label-default">{items.id_type} : {items.id_number}</p>

                        </td>
                        <td class="ty-09">
                          <p class="label label-default">Mobile No: {items.mobile_no1}</p>

                          <p class="label label-default">Email: {items.email1}</p>
                        </td>
                        <td class="ty-09">
                          <span class="label label-default">{items.date_of_birth}</span>
                        </td>


                        <td> <span className="details1">{items.address}</span>
                        </td>


                        <td >
                          <Link to={"UpdateQuotation/" + items.id} class="table-link text-info">

                            <span class="fa-stack">
                              <i class="fa fa-square fa-stack-2x"></i>
                              <i class="fa fa-pencil fa-stack-1x fa-inverse"></i>
                            </span>
                          </Link>

                          <a onClick={(event) => this.deleteUser(items.id)} class="table-link danger">
                            <span class="fa-stack">
                              <i class="fa fa-square fa-stack-2x"></i>
                              <i class="fa fa-trash-o fa-stack-1x fa-inverse"></i>
                            </span>
                          </a>
                        </td>

                      </tr>


                    )

                  }
                </Table>
                : <Table striped bordered hover >


                  <thead>
                    <tr className="notfound">
                      <p>Data not Available</p>
                    </tr>
                  </thead>
                </Table>


            }










          </div>
        </div>
      </div >
    )
  }

}
export default Clients;
