import React, { useState, useEffect } from 'react'
import axios from 'axios';
import jwt_decode from "jwt-decode";

function Login(props) {
  const [email, setemail] = useState('');
  const [password, setpassword] = useState('');
  const [errorMsg, setErrorMsg] = useState("");
  const apiUrl = "http://192.168.1.9:4000/login/";
  const Login = (e) => {
    e.preventDefault();

    const data = { email, password };
    axios.post(apiUrl, data)

      .then((result) => {

        console.log("ijogjtrgjtg", result.data);
        const serializedState = JSON.stringify(result.data);
        var a = localStorage.setItem('myData', serializedState);
        const token = JSON.parse(localStorage.getItem('myData'));
        const decoded = jwt_decode(token.jwt);
        console.log("decoded", decoded);
        props.history.push('/Deshbord');
      })
      .catch(function (error) {

        if (error.response.status === 401);
        else setErrorMsg("Username / Password wrong. Please try again later.");
      });

  };
  return (
    <div>

      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <div class=" h-100">
        <div class="d-flex justify-content-center h-100">
          <div class="user_card">
            <div class="d-flex justify-content-center">
              <div class="brand_logo_container">
                <img src="logo-01.png" class="brand_logo" alt="Logo" />
              </div>
            </div>
            <div class="d-flex justify-content-center form_container">
              <form onSubmit={Login}>
                <div class="input-group mb-3">
                  <div class="input-group-append">
                    <span class="input-group-text"><i class="fa fa-user"></i></span>
                  </div>
                  <input type="text" class="form-control" value={email} onChange={(e) => setemail(e.target.value)} name="email" id="email" placeholder="Enter email id" />
                </div>
                <div class="input-group mb-2">
                  <div class="input-group-append">
                    <span class="input-group-text"><i class="fa fa-key"></i></span>
                  </div>
                  <input type="password" class="form-control" value={password} onChange={(e) => setpassword(e.target.value)} name="password" id="DepPasswordartment" placeholder="Enter Password" />
                </div>
                <div class="form-group">
                  <div class="custom-control custom-checkbox">
                    <input type="checkbox" class="custom-control-input" id="customControlInline" />
                    <label class="custom-control-label online_order" for="customControlInline">Remember me</label>
                  </div>
                </div>
                <div class="d-flex justify-content-center mt-3 login_container">
                  <button type="submit" name="button" class="btn login_btn">Login</button>
                </div>
              </form>
            </div>

            <div class="mt-4">
              <div class="d-flex justify-content-center links online_order">
                Don't have an account? <a href="#" onClick={() => window.open("/Registration", "_self")} class="ml-2 online_order">Sign Up</a>
              </div>
              <div class="d-flex justify-content-center links online_order">
                <a href="#" class=" online_order">Forgot your password?</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Login;

