import React from 'react';

const Risklist = ({countryList=[]}) => {
  return (
    <>
    { countryList.map((data,index) => {
        if (data) {
          return (
            <div key={data.product_name}>
              <h1>{data.product_name}</h1>
	    </div>	
    	   )	
    	 }
    	 return null
    }) }
    </>
  );
}
export default Risklist