import * as React from "react";
import ReactApexChart from 'react-apexcharts'
import axios from 'axios'
export default class Pie extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
          
      series: [],
      options: {
        chart: {
          height: 390,
          type: 'radialBar',
        },
        plotOptions: {
          radialBar: {
            offsetY: 0,
            startAngle: 0,
            endAngle: 350,
            hollow: {
              margin: 5,
              size: '30%',
              background: '#0078d4',
              colors:'#000',
              image: undefined,
            },
            dataLabels: {
              name: {
                show: false,
              },
              value: {
                show: false,
              }
            }
          }
        },
        colors: ['#0084ff', '#dc3545', '#17a2b8', '#cf18b0','#ffc107'],
        labels: ['Travel', 'Fire', 'Medical', 'Vehicle', 'Others'],
        legend: {
          show: true,
          floating: true,
          fontSize: '16px',
          position: 'left',
          offsetX: 160,
          offsetY: 15,
          labels: {
            useSeriesColors: true,
          },
          markers: {
            size: 0
          },
          formatter: function(seriesName, opts) {
            return seriesName + ":  " + opts.w.globals.series[opts.seriesIndex]
          },
          itemMargin: {
            vertical: 3
          }
        },
        responsive: [{
          breakpoint: 480,
          options: {
            legend: {
                show: false
            }
          }
        }]
      },
    
    
    };
  }


  async componentDidMount() {
    var Userdetials = localStorage.getItem('myData')
    var user_data = JSON.parse(Userdetials)
    const Travel = [];
    const Fire = [];
    const Medical = [];
    const Vehicle = [];
    const Others = [];
    await axios.get("https://connexialfintech.pythonanywhere.com/policy/DeasboardPiChartViewChart/",{
      method: 'GET',
      headers: {

          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'Authorization': user_data.jwt,
      },
  })
      .then(Response => {
        console.log("Response", Response.data);
        for (const obj of Response.data) {
          console.log("obj", obj);
          Travel.push(obj.travel);
          Fire.push(obj.fire);
          Medical.push(obj.medical);
          Vehicle.push(obj.vehicle);
          Others.push(obj.other);
        }

        console.log("Travel,Fire,medical,Vehicle,Others", Travel, Fire, Medical, Vehicle, Others);
      
        this.setState({
    
          series: [Travel, Fire, Medical, Vehicle, Others],

          options: {
            labels: ['Travel', 'Fire', 'Medical', 'Vehicle', 'Others'],


          },
        })
      })
      .catch(err => {
        console.log("errr", err);
      })
  }

  render() {

    return (
    
       
      <div id="chart" className="rtchart">
      <ReactApexChart options={this.state.options} series={this.state.series} type="radialBar" height={390} />
    </div>
    )
  }
}