
import Header from "../Header";
import React from 'react';

import Row from 'react-bootstrap/Row';

import Col from 'react-bootstrap/Col';
import Table from 'react-bootstrap/Table';

class User extends React.Component {

  constructor() {
    super()
    this.state = {
      SearchData: null,


    }
  }


  SearchClient(key) {
    var Userdetials = localStorage.getItem('myData')
    var user_data = JSON.parse(Userdetials)
    console.warn(key)
    fetch(`https://connexialfintech.pythonanywhere.com/adminclientlistfilter/?client_name=` + key,{
      method: 'GET',
      headers: {

          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'Authorization': user_data.jwt,
      },
  }).then((data) => {
      data.json().then((resp) => {
        console.warn("resp", resp)
        this.setState({ SearchData: resp })

      })
    })

  }

  SearchProfile(key) {
    var Userdetials = localStorage.getItem('myData')
    var user_data = JSON.parse(Userdetials)
    console.warn(key)
    fetch(`https://connexialfintech.pythonanywhere.com/adminclientlistfilter/?id=` + key,{
      method: 'GET',
      headers: {

          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'Authorization': user_data.jwt,
      },
  }).then((data) => {
      data.json().then((resp) => {
        console.warn("resp", resp)
        this.setState({ SearchData: resp })

      })
    })

  }

  SearchLocation(key) {
    var Userdetials = localStorage.getItem('myData')
    var user_data = JSON.parse(Userdetials)
    console.warn(key)
    fetch(`https://connexialfintech.pythonanywhere.com/adminclientlistfilter/?address=` + key,{
      method: 'GET',
      headers: {

          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'Authorization': user_data.jwt,
      },
  }).then((data) => {
      data.json().then((resp) => {
        console.warn("resp", resp)
        this.setState({ SearchData: resp })

      })
    })

  }
  SearchRegistration(key) {
    var Userdetials = localStorage.getItem('myData')
    var user_data = JSON.parse(Userdetials)
    console.warn(key)
    fetch(`https://connexialfintech.pythonanywhere.com/adminclientlistfilter/?registration_no=` + key,{
      method: 'GET',
      headers: {

          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'Authorization': user_data.jwt,
      },
  }).then((data) => {
      data.json().then((resp) => {
        console.warn("resp", resp)
        this.setState({ SearchData: resp })

      })
    })

  }
  Searchdob(key) {
    var Userdetials = localStorage.getItem('myData')
    var user_data = JSON.parse(Userdetials)
    console.warn(key)
    fetch(`https://connexialfintech.pythonanywhere.com/adminclientlistfilter/?date_of_Birth=` + key,{
      method: 'GET',
      headers: {

          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'Authorization': user_data.jwt,
      },
  }).then((data) => {
      data.json().then((resp) => {
        console.warn("resp", resp)
        this.setState({ SearchData: resp })

      })
    })

  }
  deleteUser(id) {
    var Userdetials = localStorage.getItem('myData')
    var user_data = JSON.parse(Userdetials)
    fetch(`https://connexialfintech.pythonanywhere.com/policy/com/${id}`, {
      method: 'DELETE',
      headers: {
        'Accept': 'application/json',
        'Content-type': 'application/json',
        'Authorization': user_data.jwt,
      },

    }).then((result) => {
      result.json().then((resp) => {
        console.warn(resp)
        this.setState({ SearchData: "" })

      })
    })
  }

  render() {


    return (
      <div>
        <Header />
        <br></br>
        <br></br>
        <br></br>
        <br></br>
        <div id="msform">

          <div className="fstep">
            <fieldset>
              <div class="row">
                <div class="col-lg-6 col-md-6">
                  <h6>User List</h6>
                </div>
                <div class="col-lg-6 col-md-6">
                  <button className="refresh" onClick={() => window.open("/admin/Add_User")}>Add</button>
                </div>
              </div>

              <hr></hr>
              <form>

                <Row>

                  <Col xs={12} md={2} lg={2} >
                    <label for="size_1">User Name</label>
                    <input type="text" onChange={(event) => this.SearchClient(event.target.value)} name="User Name" />
                  </Col>

                  <Col xs={12} md={2} lg={2} >
                    <label for="size_1">Profile id</label>
                    <input type="text" onChange={(event) => this.SearchProfile(event.target.value)} name="Profile" />
                  </Col>
                  <Col xs={12} md={2} lg={2} >
                    <label for="size_1">Location</label>
                    <input type="text" onChange={(event) => this.SearchLocation(event.target.value)} name="Debit" />
                  </Col>
                  <Col xs={12} md={2} lg={2} >
                    <label for="size_1">Registration No</label>
                    <input type="text" onChange={(event) => this.SearchRegistration(event.target.value)} name="SearchRegistration" />
                  </Col>
                  <Col xs={12} md={2} lg={2} >
                    <label for="size_1">Date of Birth</label>
                    <input type="date" onChange={(event) => this.Searchdob(event.target.value)} name="Vehicle" />
                  </Col>
                  
                </Row>
              </form>
            </fieldset>



          </div>

          <div className="fstep">

            {
              this.state.SearchData ?
                <Table striped bordered hover >


                  <thead>
                    <tr>

                      <th><span>ID</span></th>
                      <th><sapn>USER NAME</sapn></th>
                      <th><span>CONTACT INFORMATION </span></th>
                      <th><span>REGISTRATION NO</span></th>
                      <th><span>DATE OF JOINING</span></th>
                      <th><span>LOCATION</span></th>
                     
                      <th><span>ACTION</span></th>

                    </tr>
                  </thead>



                  {
                    this.state.SearchData.map((items) =>


                      <tr>
                        <td class="ty-09">
                          <p class="tk-10"><span className="details">{items.insurance_Type}</span></p>
                        </td>

                        <td class="ty-09">
                          <span class="label label-default">{items.date}</span>
                        </td>
                        <td class="ty-09">
                          <span class="label label-default">{items.debit_No_Tax_Invoice}</span>
                        </td>
                        <td class="ty-09">
                          <span class="label label-default">{items.quote_No}</span>
                        </td>
                        <td class=" ty-09">
                          <span className="details1">{items.client_Name}</span>
                        </td>
                        

                        <td>
                          <span class="label label-default">{items.total_Amount}</span>
                        </td>

                        <td> <span className="details1">{items.amount_Paid}</span>
                        </td>
                        <td class="ty-09">
                          <span class="label label-default">{items.balance}</span>
                        </td>

                        <td> <span className="details1">{items.ccv}</span>
                        </td>
                        
                        <td> <span className="details1">{items.status}</span>
                        </td>
                       

                        <td >
                          <a href="#" class="table-link text-warning">
                            <span class="fa-stack">
                              <i class="fa fa-square fa-stack-2x"></i>
                              <i class="fa fa-search-plus fa-stack-1x fa-inverse"></i>
                            </span>
                          </a>

                          <a onClick={(event) => this.deleteUser(items.id)} class="table-link danger">
                            <span class="fa-stack">
                              <i class="fa fa-square fa-stack-2x"></i>
                              <i class="fa fa-trash-o fa-stack-1x fa-inverse"></i>
                            </span>
                          </a>
                        </td>

                      </tr>


                    )

                  }
                </Table>
                : <Table striped bordered hover >


                  <thead>
                    <tr className="notfound">
                      <p>Data not Available</p>
                    </tr>
                  </thead>
                </Table>


            }










          </div>
        </div>
      </div >
    )
  }

}
export default User;
